import {GitHubAvatar} from '@github-ui/github-avatar'
import {templateReposPath} from '@github-ui/paths'
import {verifiedFetchJSON} from '@github-ui/verified-fetch'
import {ActionList, ActionMenu, Box, Checkbox, FormControl} from '@primer/react'
import {useState} from 'react'

import type {TemplateRepo} from '../types/repo-creation-types'

interface TemplateReposData {
  templateRepos: TemplateRepo[]
}

const label = 'Repository template'

export function RepoTemplateMenu({
  selectedTemplateRepo,
  onRepoTemplateSelect,
  includeAllBranches,
  onIncludeAllBranchesChange,
  onOpen,
}: {
  selectedTemplateRepo?: TemplateRepo
  onRepoTemplateSelect: (selectedTemplateRepo?: TemplateRepo) => void
  includeAllBranches: boolean
  onIncludeAllBranchesChange: (includeAllBranches: boolean) => void
  onOpen?: () => void
}) {
  const [templateRepos, setTemplateRepos] = useState<TemplateRepo[] | undefined>()
  const [errorLoadingTemplateRepos, setErrorLoadingTemplateRepos] = useState(false)

  const onOpenMenu = async (open: boolean) => {
    loadTemplateRepos(open)
    open && onOpen?.()
  }

  const loadTemplateRepos = async (open: boolean) => {
    if (open && !templateRepos) {
      try {
        const result = await verifiedFetchJSON(templateReposPath())

        const data: TemplateReposData = await result.json()
        if (!result.ok || !data.templateRepos) {
          handleErrorLoadingTemplateRepos()
          return
        }

        setErrorLoadingTemplateRepos(false)

        // if a repo was passed in with the initial payload, always include it in the dropdown
        if (selectedTemplateRepo && !data.templateRepos.some(x => x.id === selectedTemplateRepo.id)) {
          data.templateRepos.unshift(selectedTemplateRepo)
        }

        setTemplateRepos(data.templateRepos)
      } catch (error) {
        handleErrorLoadingTemplateRepos()
      }
    }
  }

  const headerButton = () => {
    const displayName = selectedTemplateRepo?.nameWithDisplayOwner || 'No template'
    const buttonAvatar = selectedTemplateRepo && (() => <GitHubAvatar src={selectedTemplateRepo.avatarUrl} />)
    return (
      <ActionMenu.Button aria-label={`${label}: ${displayName}`} leadingVisual={buttonAvatar}>
        {displayName}
      </ActionMenu.Button>
    )
  }

  const handleErrorLoadingTemplateRepos = () => {
    setTemplateRepos(undefined)
    setErrorLoadingTemplateRepos(true)
  }

  const dropdownContent = () => {
    const hasLoadedTemplateRepos = templateRepos !== undefined

    if (hasLoadedTemplateRepos && templateRepos.length > 0) {
      return (
        <RepoTemplateItems
          templateRepos={templateRepos}
          selectedTemplateRepo={selectedTemplateRepo}
          onSelect={onRepoTemplateSelect}
        />
      )
    } else if (hasLoadedTemplateRepos && templateRepos?.length === 0) {
      return (
        <ActionList.Item key="no-templates" disabled={true} sx={{p: 3}}>
          No templates available.
        </ActionList.Item>
      )
    } else if (errorLoadingTemplateRepos) {
      return (
        <ActionList.Item key="error-loading-templates" disabled={true} sx={{p: 3, color: 'danger.fg'}}>
          An error occurred while loading templates. Please reopen the dropdown to try again.
        </ActionList.Item>
      )
    } else if (!hasLoadedTemplateRepos && !errorLoadingTemplateRepos) {
      return (
        <ActionList.Item key="fetching-templates" disabled={true}>
          Fetching templates
        </ActionList.Item>
      )
    } else {
      return null
    }
  }

  return (
    <Box sx={{borderColor: 'border.muted', borderBottomWidth: 1, borderBottomStyle: 'solid', py: 3}}>
      <FormControl>
        <FormControl.Label>{label}</FormControl.Label>
        <FormControl.Caption>Start your repository with a template repository&apos;s contents.</FormControl.Caption>
        <ActionMenu onOpenChange={onOpenMenu}>
          {headerButton()}
          <ActionMenu.Overlay width="small" maxHeight="large" sx={{overflow: 'auto'}}>
            {dropdownContent()}
          </ActionMenu.Overlay>
        </ActionMenu>
      </FormControl>
      {selectedTemplateRepo && (
        <FormControl sx={{mt: 2}}>
          <FormControl.Label>Include all branches</FormControl.Label>
          <FormControl.Caption>
            Copy all branches from {selectedTemplateRepo.nameWithDisplayOwner} and not just the default branch.
          </FormControl.Caption>
          <Checkbox checked={includeAllBranches} onChange={() => onIncludeAllBranchesChange(!includeAllBranches)} />
        </FormControl>
      )}
    </Box>
  )
}

function RepoTemplateItems({
  templateRepos,
  selectedTemplateRepo,
  onSelect,
}: {
  templateRepos: TemplateRepo[]
  selectedTemplateRepo?: TemplateRepo
  onSelect: (selectedTemplateRepo?: TemplateRepo) => void
}) {
  const templateListItems = templateRepos.map(templateRepo => {
    const {id, nameWithDisplayOwner, avatarUrl} = templateRepo
    return (
      <ActionList.Item selected={selectedTemplateRepo?.id === id} key={id} onSelect={() => onSelect(templateRepo)}>
        <ActionList.LeadingVisual>
          <GitHubAvatar src={avatarUrl} />
        </ActionList.LeadingVisual>
        {nameWithDisplayOwner}
      </ActionList.Item>
    )
  })

  const noTemplateItem = (
    <ActionList.Item selected={!selectedTemplateRepo} onSelect={() => onSelect(undefined)} key="No template">
      No template
    </ActionList.Item>
  )

  return <ActionList selectionVariant="single">{[noTemplateItem, ...templateListItems]}</ActionList>
}

try{ RepoTemplateMenu.displayName ||= 'RepoTemplateMenu' } catch {}
try{ RepoTemplateItems.displayName ||= 'RepoTemplateItems' } catch {}