import {GitIgnoreDropdown} from '@github-ui/git-ignore-dropdown'
import {GitBranchIcon} from '@primer/octicons-react'
import {Box, Checkbox, FormControl, Link, Text} from '@primer/react'

import {useUserAnalytics} from '../hooks/use-user-analytics'
import type {License, OwnerSelection} from '../types/repo-creation-types'
import {LicensePicker} from './LicensePicker'

export function InitializeRepositoryOptions({
  addReadme,
  onAddReadmeChange,
  licensePickerAvailable,
  ownerSelection,
  licenses,
  selectedLicense,
  onLicenseChange,
  selectedGitignoreTemplate,
  onGitignoreTemplateChange,
  currentUserLogin,
  helpUrl,
  docsUrls,
}: {
  addReadme: boolean
  onAddReadmeChange: (newAddReadme: boolean) => void
  licensePickerAvailable: boolean
  ownerSelection?: OwnerSelection
  licenses: License[]
  selectedLicense?: License
  onLicenseChange: (newLicense: License) => void
  selectedGitignoreTemplate: string
  onGitignoreTemplateChange: (newGitignoreTemplate: string) => void
  currentUserLogin: string
  helpUrl: string
  docsUrls: {
    licensePicker: string
  }
}) {
  const {sendRepoFormClickEvent} = useUserAnalytics()

  return (
    <div>
      <FormControl sx={{mb: 3}}>
        <FormControl.Label>Initialize this repository with:</FormControl.Label>
        <FormControl>
          <FormControl.Label>Add a README file</FormControl.Label>
          <FormControl.Caption>
            This is where you can write a long description for your project.{' '}
            <Link
              inline
              href={`${helpUrl}/github/creating-cloning-and-archiving-repositories/about-readmes`}
              target="_blank"
            >
              Learn more about READMEs.
            </Link>
          </FormControl.Caption>
          <Checkbox
            checked={addReadme}
            onChange={() => {
              sendRepoFormClickEvent('README_CHECKBOX', {
                action: 'change',
                option: addReadme,
              })
              onAddReadmeChange(!addReadme)
            }}
          />
        </FormControl>
      </FormControl>
      <FormControl sx={{mb: 3}}>
        <FormControl.Label htmlFor="gitignore-dropdown">Add .gitignore</FormControl.Label>
        <FormControl.Caption id="gitignore-dropdown-caption">
          Choose which files not to track from a list of templates.{' '}
          <Link inline href={`${helpUrl}/github/using-git/ignoring-files`} target="_blank">
            Learn more about ignoring files.
          </Link>
        </FormControl.Caption>
        <Box sx={{my: 1}}>
          <GitIgnoreDropdown
            selectedTemplate={selectedGitignoreTemplate}
            onSelect={gitingore => {
              sendRepoFormClickEvent('GITIGNORE_SELECT', {
                action: 'select',
                option: gitingore,
              })
              onGitignoreTemplateChange(gitingore)
            }}
            onOpen={() =>
              sendRepoFormClickEvent('GITIGNORE_SELECT', {
                action: 'open',
              })
            }
            allowNoneOption={true}
            textInputProps={{id: 'gitignore-dropdown', 'aria-describedby': 'gitignore-dropdown-caption'}}
          />
        </Box>
      </FormControl>
      {licensePickerAvailable && (
        <LicensePicker
          licenses={licenses}
          selectedLicense={selectedLicense}
          onLicenseSelection={onLicenseChange}
          docsUrl={docsUrls.licensePicker}
        />
      )}
      {addReadme && ownerSelection && (
        <Box sx={{mb: 2}}>
          <DefaultBranchMessage ownerSelection={ownerSelection} currentUserLogin={currentUserLogin} />
        </Box>
      )}
    </div>
  )
}

function DefaultBranchMessage({
  ownerSelection,
  currentUserLogin,
}: {
  ownerSelection: OwnerSelection
  currentUserLogin: string
}) {
  return (
    <Text sx={{color: 'fg.muted'}}>
      This will set{' '}
      <span className="commit-ref">
        <GitBranchIcon />
        {ownerSelection.defaultNewRepoBranch}
      </span>{' '}
      as the default branch.
      {ownerSelection.defaultBranchSettingsUrl && (
        <span>
          {' '}
          Change the default name in {currentUserLogin === ownerSelection.name
            ? 'your'
            : `${ownerSelection.name}'s`}{' '}
          <Link aria-label="Branch settings link" href={ownerSelection.defaultBranchSettingsUrl} inline>
            settings
          </Link>
          .
        </span>
      )}
    </Text>
  )
}

try{ InitializeRepositoryOptions.displayName ||= 'InitializeRepositoryOptions' } catch {}
try{ DefaultBranchMessage.displayName ||= 'DefaultBranchMessage' } catch {}