import {OwnerDropdown, type OwnerItem} from '@github-ui/owner-dropdown'
import {Box, FormControl, Radio, RadioGroup, TextInput} from '@primer/react'
import {forwardRef, useImperativeHandle, useRef} from 'react'

import {useRepoOwnerItemsPath} from '../hooks/use-repo-owner-items-path'
import {TransferOwnerInputType} from '../types/repo-creation-types'

export const OwnerRadioButtonSelection = forwardRef(
  (
    {
      ownerItems,
      excludedOrg,
      selectedOwner,
      hideSelectOwnerCheck = false,
      onCurrentDropdownSelectionChange,
      onCurrentFreeformInputChange,
      currentFreeformInput,
      selectedRadio,
      onRadioChange,
      ownerValidationError,
    }: {
      /** List of owners to display. If undefined, the list will be loaded from the server. */
      ownerItems?: OwnerItem[]
      excludedOrg?: string
      selectedOwner?: OwnerItem
      hideSelectOwnerCheck?: boolean
      onCurrentDropdownSelectionChange: (value: OwnerItem) => void
      currentFreeformInput: string
      onCurrentFreeformInputChange: (value: string) => void
      selectedRadio: TransferOwnerInputType
      onRadioChange: (value: TransferOwnerInputType) => void
      ownerValidationError: string
    },
    ref,
  ) => {
    const repoOwnerItemsPath = useRepoOwnerItemsPath()
    const existingOrgsSelected = selectedRadio === TransferOwnerInputType.ExistingOrgs
    const freeformInputSelected = selectedRadio === TransferOwnerInputType.FreeformInput

    const existingOrgsDisabled = ownerItems?.length === 0

    const orgSelectionText = `Select one of my organizations${existingOrgsDisabled ? ' (none available)' : ''}`

    const freeformOwnerInputRef = useRef<HTMLInputElement>(null)
    const ownerDropdownRef = useRef<HTMLButtonElement>(null)

    useImperativeHandle(ref, () => ({
      focusOwnerDropdown: () => {
        ownerDropdownRef.current?.focus()
      },
      focusFreeformOwnerInput: () => {
        freeformOwnerInputRef.current?.focus()
      },
    }))

    const ownerRadioButtonSelectionValidationId = 'owner-radio-button-selection-validation'

    return (
      <Box sx={{gap: 3, display: 'grid'}}>
        <RadioGroup name="choiceGroup" required onChange={value => onRadioChange(value as TransferOwnerInputType)}>
          <RadioGroup.Label sx={{mb: 1}}>New owner</RadioGroup.Label>
          <FormControl disabled={existingOrgsDisabled}>
            <Radio sx={{mt: 1}} value={TransferOwnerInputType.ExistingOrgs} checked={existingOrgsSelected} />
            <FormControl.Label>
              <p>{orgSelectionText}</p>
              {existingOrgsSelected && (
                <Box sx={{mt: 2, mb: 2}}>
                  <OwnerDropdown
                    aria-describedby={ownerRadioButtonSelectionValidationId}
                    ref={ownerDropdownRef}
                    initialOwnerItems={ownerItems}
                    ownerItemsPath={repoOwnerItemsPath}
                    excludedOrg={excludedOrg}
                    hideSelectOwnerCheck={hideSelectOwnerCheck}
                    selectedOwner={selectedOwner}
                    onOwnerChange={value => {
                      onCurrentDropdownSelectionChange(value)
                    }}
                    showLabel={false}
                  />
                </Box>
              )}
            </FormControl.Label>
          </FormControl>
          <FormControl>
            <Radio sx={{mt: 1}} value={TransferOwnerInputType.FreeformInput} checked={freeformInputSelected} />
            <FormControl.Label>
              <p>Specify an organization or username</p>
              {freeformInputSelected && (
                <Box sx={{mt: 2, mb: 2}}>
                  <TextInput
                    block
                    aria-describedby={ownerRadioButtonSelectionValidationId}
                    aria-label="Specify an organization or username"
                    ref={freeformOwnerInputRef}
                    name="Specify an organization or username"
                    value={currentFreeformInput}
                    onChange={e => onCurrentFreeformInputChange(e.target.value)}
                    data-testid="freeform-text-input"
                  />
                </Box>
              )}
            </FormControl.Label>
          </FormControl>
          {ownerValidationError && (
            <FormControl.Validation id={ownerRadioButtonSelectionValidationId} variant="error">
              {ownerValidationError}
            </FormControl.Validation>
          )}
        </RadioGroup>
      </Box>
    )
  },
)

OwnerRadioButtonSelection.displayName = 'OwnerRadioButtonSelection'
