import {FormControl, Text, TextInput} from '@primer/react'
import {forwardRef, useEffect} from 'react'

export const Description = forwardRef(
  (
    {
      description,
      onDescriptionChange,
      tooLong,
      setTooLong,
    }: {
      description: string
      onDescriptionChange: (value: string) => void
      tooLong: boolean
      setTooLong: (value: boolean) => void
    },
    ref: React.ForwardedRef<HTMLInputElement>,
  ) => {
    useEffect(() => {
      setTooLong(description.length > 350)
    }, [description, setTooLong])

    return (
      <FormControl>
        <FormControl.Label>
          Description <Text sx={{fontSize: 0, color: 'fg.muted'}}>(optional)</Text>
        </FormControl.Label>
        <TextInput
          ref={ref}
          block
          aria-label="Description"
          name="Description"
          value={description}
          onChange={e => onDescriptionChange(e.target.value)}
          validationStatus={tooLong ? 'error' : undefined}
        />
        {tooLong && (
          <FormControl.Validation variant="error">
            Description cannot be more than 350 characters
          </FormControl.Validation>
        )}
      </FormControl>
    )
  },
)

Description.displayName = 'Description'
