import {AnalyticsProvider} from '@github-ui/analytics-provider'
import {useCurrentRepository} from '@github-ui/current-repository'
import type {OwnerItem} from '@github-ui/owner-dropdown'
import {forkPath, forksListPath, repositoryPath} from '@github-ui/paths'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {SafeHTMLString} from '@github-ui/safe-html'
import {useNavigate} from '@github-ui/use-navigate'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {AlertIcon} from '@primer/octicons-react'
import {Box, BranchName, Button, Checkbox, Flash, FormControl, Link, Octicon, Text} from '@primer/react'
import {useMemo, useRef, useState} from 'react'

import {CreationSummary} from '../components/CreationSummary'
import {Description} from '../components/Description'
import {DisabledCreationInPersonalNamespaceMessage} from '../components/InfoMessage'
import {OwnerRepoSelection, type OwnerRepoSelectionRef} from '../components/OwnerRepoSelection'
import {RepoCreationForm} from '../components/RepoCreationForm'
import {CurrentFormTypeProvider} from '../contexts/CurrentForm'
import {useUserAnalytics} from '../hooks/use-user-analytics'
import type {ForkRepoListPayload, ForkRepoPayload} from '../types/repo-creation-types'

export default function Fork() {
  const appName = 'repo-creation'
  const category = 'fork'
  const metadata = useMemo(() => ({}), [])

  return (
    <AnalyticsProvider appName={appName} category={category} metadata={metadata}>
      <CurrentFormTypeProvider type="fork">
        <ForkPage />
      </CurrentFormTypeProvider>
    </AnalyticsProvider>
  )
}

function ForkPage() {
  const payload = useRoutePayload<ForkRepoPayload | ForkRepoListPayload>()

  if (isForkListPayload(payload)) {
    return <CannotCreateFork payload={payload} />
  }

  return <ForkForm payload={payload} />
}

function ForkForm({payload}: {payload: ForkRepoPayload}) {
  const navigate = useNavigate()
  const repo = useCurrentRepository()

  const [isForking, setIsForking] = useState(false)

  const {repoCreate, initialOwnerSelection, repoHasForks, repoDescription} = payload
  const {ownerItems, protectedOrgLogins, restrictCreateRepositoriesInPersonalNamespace, helpUrl} = repoCreate

  const [owner, changeOwner] = useState<OwnerItem | undefined>(initialOwnerSelection)

  const [repoName, setRepoName] = useState(repo.name)
  const [repoNameValid, setRepoNameValid] = useState(true)

  const [description, setDescription] = useState(repoDescription || '')
  const [descriptionValid, setDescriptionValid] = useState(true)

  const [forkOneBranch, setForkOneBranch] = useState(true)
  const [flash, setFlash] = useState('')

  const [submitClicked, setSubmitClicked] = useState(false)

  const {sendRepoFormClickEvent} = useUserAnalytics()

  const ownerRepoSelectionRef = useRef<OwnerRepoSelectionRef>()
  const descriptionRef = useRef<HTMLInputElement>(null)

  async function createFork() {
    setSubmitClicked(true)
    sendRepoFormClickEvent('SUBMIT_BUTTON', {
      action: 'submit',
    })

    if (!owner) {
      ownerRepoSelectionRef.current?.focusOwnerDropdown()
      return
    }

    if (!repoNameValid) {
      ownerRepoSelectionRef.current?.focusRepoInput()
      return
    }

    if (!descriptionValid) {
      descriptionRef.current?.focus()
      return
    }

    if (isForking) {
      return
    }

    const formData = new FormData()

    formData.append('owner', owner.name)
    formData.append('fork_repository[name]', repoName)
    formData.append('fork_repository[description]', description)
    formData.append('fork_repository[one_branch_fork]', `${forkOneBranch ? 1 : 0}`)

    setIsForking(true)
    try {
      const result = await verifiedFetch(forkPath({repo: repo.name, owner: repo.ownerLogin}), {
        method: 'POST',
        body: formData,
        headers: {Accept: 'text/html'},
      })

      if (result.redirected) {
        navigate(result.url)
      } else if (!result.ok) {
        const {data} = await result.json()
        setFlash(data.error)
      }
    } catch (e) {
      setFlash('Something went wrong!')
    }

    setIsForking(false)
  }

  return (
    <RepoCreationForm
      title="Create a new fork"
      flash={flash as SafeHTMLString}
      description={<RepoDescription repoHasForks={repoHasForks} />}
      protectedOrgLogins={protectedOrgLogins}
      onSubmit={createFork}
      submitButton={
        <Button type="submit" disabled={isForking} variant="primary">
          {isForking ? 'Creating fork' : 'Create fork'}
        </Button>
      }
    >
      <Box sx={{pt: 3, mb: 2}}>
        <OwnerRepoSelection
          ref={ownerRepoSelectionRef}
          selectedOwner={owner}
          ownerItems={ownerItems}
          repositoryName={repoName}
          onOwnerChange={changeOwner}
          onRepoChange={setRepoName}
          onValidityChange={setRepoNameValid}
          hideSelectOwnerCheck={!submitClicked}
        />
      </Box>

      <Text as="div" sx={{mb: 3}}>
        By default, forks are named the same as their upstream repository. You can customize the name to distinguish it
        further.
      </Text>

      {restrictCreateRepositoriesInPersonalNamespace && (
        <Box sx={{my: 3}}>
          <DisabledCreationInPersonalNamespaceMessage />
        </Box>
      )}

      <Box sx={{mb: 3}}>
        <Description
          ref={descriptionRef}
          description={description}
          onDescriptionChange={setDescription}
          tooLong={!descriptionValid}
          setTooLong={tooLong => setDescriptionValid(!tooLong)}
        />
      </Box>

      <Box as="hr" sx={{my: 3}} />

      <FormControl sx={{mb: 3}}>
        <FormControl.Label>
          Copy the{' '}
          <BranchName as="span" sx={{fontWeight: 400}}>
            {repo.defaultBranch}
          </BranchName>{' '}
          branch only
        </FormControl.Label>
        <FormControl.Caption>
          Contribute back to {repo.ownerLogin}/{repo.name} by adding your own branch.{' '}
          <Link
            inline
            href={`${helpUrl}/pull-requests/collaborating-with-pull-requests/proposing-changes-to-your-work-with-pull-requests/creating-and-deleting-branches-within-your-repository`}
            target="_blank"
          >
            Learn more.
          </Link>
        </FormControl.Caption>
        <Checkbox
          checked={forkOneBranch}
          onChange={() => {
            const newValue = !forkOneBranch
            sendRepoFormClickEvent('ONE_BRANCH_CHECKBOX', {
              action: 'change',
              option: newValue ? 'one' : 'all',
            })
            setForkOneBranch(newValue)
          }}
        />
      </FormControl>

      <Box sx={{mb: 3}}>
        <CreationSummary owner={owner} action="fork" topBorder />
      </Box>
    </RepoCreationForm>
  )
}

function CannotCreateFork({payload}: {payload: ForkRepoListPayload}) {
  const {existingForks, truncated} = payload
  const repoHasForks = existingForks.length > 0

  const repo = useCurrentRepository()
  const forkListUrl = forksListPath({repo: repo.name, owner: repo.ownerLogin})

  return (
    <RepoCreationForm
      title="Create a new fork"
      flash={'' as SafeHTMLString}
      description={<RepoDescription repoHasForks={repoHasForks} />}
      protectedOrgLogins={[]}
    >
      <Box sx={{mb: 2}}>No available destinations to fork this repository.</Box>
      {truncated && (
        <Flash sx={{mb: 3}} data-testid="truncation-banner" variant="warning">
          <Box sx={{display: 'flex', gap: 1}}>
            <div>
              <Octicon icon={AlertIcon} />
            </div>
            <div>
              The list is limited to {existingForks.length} forks.{' '}
              <Link inline href={forkListUrl}>
                View all existing forks.
              </Link>
            </div>
          </Box>
        </Flash>
      )}
      {repoHasForks && (
        <Box as="ul" sx={{listStyle: 'none'}}>
          {existingForks.map(fork => {
            const url = repositoryPath({repo: fork.name, owner: fork.ownerLogin})
            const text = `${fork.ownerLogin}/${fork.name}`
            return (
              <li key={text}>
                <Link href={url}>{text}</Link>
              </li>
            )
          })}
        </Box>
      )}
    </RepoCreationForm>
  )
}

function RepoDescription({repoHasForks}: {repoHasForks: boolean}) {
  const repo = useCurrentRepository()
  const forkListUrl = forksListPath({repo: repo.name, owner: repo.ownerLogin})

  return (
    <span>
      A <i>fork</i> is a copy of a repository. Forking a repository allows you to freely experiment with changes without
      affecting the original project.{' '}
      {repoHasForks && (
        <Link inline href={forkListUrl}>
          View existing forks.
        </Link>
      )}
    </span>
  )
}

function isForkListPayload(payload: ForkRepoPayload | ForkRepoListPayload): payload is ForkRepoListPayload {
  return 'existingForks' in payload
}

try{ Fork.displayName ||= 'Fork' } catch {}
try{ ForkPage.displayName ||= 'ForkPage' } catch {}
try{ ForkForm.displayName ||= 'ForkForm' } catch {}
try{ CannotCreateFork.displayName ||= 'CannotCreateFork' } catch {}
try{ RepoDescription.displayName ||= 'RepoDescription' } catch {}