import type {OwnerItem} from '@github-ui/owner-dropdown'
import type {Visibility} from '@github-ui/repos-list/repos-list-types'
import type {SafeHTMLString} from '@github-ui/safe-html'
import {Box} from '@primer/react'
import {forwardRef} from 'react'

import type {OwnerSelection} from '../types/repo-creation-types'
import {OwnerRepoSelection} from './OwnerRepoSelection'
import {VisibilitySelection} from './VisibilitySelection'

export const OwnerRepoVisibilitySelection = forwardRef(
  (
    {
      children,
      ownerSelection,
      onOwnerChange,
      repositoryName,
      setRepositoryName,
      repoNameChanged,
      setRepoNameChanged,
      selectedVisibility,
      setUserSelectedVisibility,
      setIsValidRepoName,
      ownerItems,
      submitClicked,
      privateModeEnabled,
      helpUrl,
      tradeControlsPrivateRepoCreationWarning,
      tradeControlsUserPrivateRepoCreationWarning,
    }: {
      children?: React.ReactNode
      ownerItems?: OwnerItem[]
      ownerSelection?: OwnerSelection
      repositoryName: string
      setRepositoryName: (repoName: string) => void
      onOwnerChange: (selectedOwner: OwnerItem) => void
      submitClicked: boolean
      repoNameChanged: boolean
      setRepoNameChanged: (changed: boolean) => void
      readonlyRepo?: boolean
      setIsValidRepoName?: (valid: boolean) => void
      privateModeEnabled: boolean
      helpUrl: string
      selectedVisibility: Visibility
      setUserSelectedVisibility: (visibility: Visibility) => void
      tradeControlsPrivateRepoCreationWarning: SafeHTMLString
      tradeControlsUserPrivateRepoCreationWarning: SafeHTMLString
      repoNameExistsEmpty?: boolean
      setRepoNameExistsEmpty?: (existsEmpty: boolean) => void
    },
    ref,
  ) => {
    return (
      <Box
        sx={{
          borderBottomColor: 'border.muted',
          borderBottomWidth: 1,
          borderBottomStyle: 'solid',
        }}
      >
        <Box
          sx={{
            borderBottomColor: 'border.muted',
            borderBottomWidth: 1,
            borderBottomStyle: 'solid',
            pb: 3,
          }}
        >
          <OwnerRepoSelection
            ref={ref}
            ownerItems={ownerItems}
            selectedOwner={ownerSelection}
            repositoryName={repositoryName}
            onRepoChange={e => {
              setRepoNameChanged(true)
              setRepositoryName(e)
            }}
            onOwnerChange={onOwnerChange}
            hideRepoBlankCheck={!submitClicked && !repoNameChanged}
            hideSelectOwnerCheck={!submitClicked}
            onValidityChange={valid => setIsValidRepoName && setIsValidRepoName(valid)}
          />
          {children}
        </Box>
        <Box sx={{mt: 3}}>
          <Box sx={{mb: 2}}>
            <VisibilitySelection
              ownerSelection={ownerSelection}
              privateModeEnabled={privateModeEnabled}
              helpUrl={helpUrl}
              selectedVisibility={selectedVisibility}
              onChange={setUserSelectedVisibility}
              tradeControlsPrivateRepoCreationWarning={tradeControlsPrivateRepoCreationWarning}
              tradeControlsUserPrivateRepoCreationWarning={tradeControlsUserPrivateRepoCreationWarning}
            />
          </Box>
        </Box>
      </Box>
    )
  },
)

OwnerRepoVisibilitySelection.displayName = 'OwnerRepoVisibilitySelection'
