import {repositoryImportsPath} from '@github-ui/paths'
import {useFeatureFlag} from '@github-ui/react-core/use-feature-flag'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {Visibility} from '@github-ui/repos-list/repos-list-types'
import {useNavigate, useSearchParams} from '@github-ui/use-navigate'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {AlertFillIcon} from '@primer/octicons-react'
import {Box, Button, Flash, FormControl, Heading, Link, Text, TextInput} from '@primer/react'
import {useId, useRef, useState} from 'react'

import {CreationSummary} from '../components/CreationSummary'
import type {OwnerRepoSelectionRef} from '../components/OwnerRepoSelection'
import {OwnerRepoVisibilitySelection} from '../components/OwnerRepoVisibilitySelection'
import {RepoCreationForm} from '../components/RepoCreationForm'
import {useSelectedOwner} from '../hooks/use-selected-owner'
import type {ImportRepoPayload} from '../types/repo-creation-types'

export default function Import() {
  const {
    repoCreate,
    initialOwnerSelection,
    privateModeEnabled,
    tradeControlsPrivateRepoCreationWarning,
    tradeControlsUserPrivateRepoCreationWarning,
  } = useRoutePayload<ImportRepoPayload>()

  const [params] = useSearchParams()

  const {ownerItems, protectedOrgLogins, helpUrl} = repoCreate
  const {ownerSelection, changeOwner, fetchingOwnerDetails} = useSelectedOwner(initialOwnerSelection)
  const [repositoryName, setRepositoryName] = useState(params.get('repo') ?? '')
  const [userSelectedVisibility, setUserSelectedVisibility] = useState<Visibility>()
  const [vcsUrl, setVcsUrl] = useState(params.get('import_url') ?? '')
  const [flash, setFlash] = useState('')
  const [submitClicked, setSubmitClicked] = useState(false)
  const [isImporting, setIsImporting] = useState(false)
  const [repoNameChanged, setRepoNameChanged] = useState(false)
  const [sourceUsername, setSourceUsername] = useState(params.get('sourceUsername') ?? '')
  const [sourceToken, setSourceToken] = useState(params.get('sourceToken') ?? '')
  // Initialize to false because repo name is initially blank
  const [isValidRepoName, setIsValidRepoName] = useState(false)
  const warningValidationId = useId()

  const ownerRepoVisibilitySelectionRef = useRef<OwnerRepoSelectionRef>()
  const vcsUrlRef = useRef<HTMLInputElement>(null)

  const navigate = useNavigate()

  // The default visibility is 'private' if no owner is selected, per:
  // app/components/repositories/new/owner_empty_selection_option.rb#L1
  const selectedVisibility = userSelectedVisibility || ownerSelection?.defaultVisibility || 'private'

  const isGitHubImporterOnActionsEnabled = useFeatureFlag('github_importer_on_actions')

  const unsupportedProtocol = (() => {
    if (vcsUrl.startsWith('svn://')) {
      return 'svn://'
    } else if (vcsUrl.startsWith('git://')) {
      return 'git://'
    } else {
      return ''
    }
  })()

  const isValidHttpUrl = (() => {
    const pattern = new RegExp(
      '^(https?://)?' + // protocol
        '((([a-z\\d]([-]?[a-z\\d])*)@)?' + // optional username
        '(([a-z\\d]([-]?[a-z\\d])*)\\.)+' + // domain name
        '([a-z]{2,}|([a-z0-9-]+\\.[a-z]{2,})))' + // TLD or SLD
        '((\\d{1,3}\\.){3}\\d{1,3})?' + // OR ip (v4) address (optional)
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
        '(\\#[-a-z\\d_]*)?$', // fragment locator
      'i',
    )
    return pattern.test(vcsUrl)
  })()

  const vcsPlaceholderText = isGitHubImporterOnActionsEnabled
    ? 'https://git.example.org/code/git'
    : 'https://svn.example.org/code/svn'
  const sourceRepoHeaderText = isGitHubImporterOnActionsEnabled
    ? 'Your source repository details'
    : 'Your old repository details'
  const vcsUrlText = isGitHubImporterOnActionsEnabled
    ? 'The URL for your source repository'
    : "Your old repository's clone URL"
  const vcsUrlLabel = isGitHubImporterOnActionsEnabled
    ? 'The URL for your source repository'
    : "Your old repository's clone URL"

  const importHandler = async () => {
    setSubmitClicked(true)
    if (isImporting || fetchingOwnerDetails) return
    if (vcsUrl && isValidHttpUrl && isValidRepoName && ownerSelection) {
      const formData = new FormData()
      formData.append('vcs_url', vcsUrl)
      formData.append('owner', ownerSelection.name)
      formData.append('repository[name]', repositoryName)
      formData.append('repository[visibility]', selectedVisibility)
      formData.append('source_username', sourceUsername)
      formData.append('source_access_token', sourceToken)

      try {
        setIsImporting(true)
        const result = await verifiedFetch(repositoryImportsPath(), {
          method: 'POST',
          body: formData,
          headers: {Accept: 'text/html'},
        })

        if (result.redirected) {
          navigate(result.url)
        } else if (!result.ok) {
          const json = await result.json()
          setFlash(json.data.error)
          setIsImporting(false)
        }
      } catch (e) {
        setFlash('Something went wrong!')
        setIsImporting(false)
      }
    } else if (!vcsUrl || !isValidHttpUrl) {
      vcsUrlRef.current?.focus()
      return
    } else if (!ownerSelection) {
      ownerRepoVisibilitySelectionRef.current?.focusOwnerDropdown()
      return
    } else {
      ownerRepoVisibilitySelectionRef.current?.focusRepoInput()
      return
    }
  }

  const ownerRepoVisibilitySelectionProps = {
    ownerItems,
    ownerSelection,
    repositoryName,
    setRepositoryName,
    onOwnerChange: changeOwner,
    submitClicked,
    repoNameChanged,
    setRepoNameChanged,
    setIsValidRepoName,
    privateModeEnabled,
    helpUrl,
    selectedVisibility,
    setUserSelectedVisibility,
    tradeControlsPrivateRepoCreationWarning,
    tradeControlsUserPrivateRepoCreationWarning,
  }

  return (
    <RepoCreationForm
      flash={flash}
      title="Import your project to GitHub"
      description="Import all the files, including revision history, from another version control system."
      protectedOrgLogins={protectedOrgLogins}
      onSubmit={importHandler}
      submitButton={
        <Box sx={{display: 'flex', mt: 3}}>
          <Button type="button" variant="invisible" sx={{mr: 1}} onClick={() => navigate('/')}>
            Cancel
          </Button>
          <Button type="submit" variant="primary" disabled={isImporting}>
            {isImporting ? 'Preparing import' : 'Begin import'}
          </Button>
        </Box>
      }
    >
      <Flash variant="warning" sx={{mb: 3, mt: 3}}>
        Support for importing Mercurial, Subversion and Team Foundation Version Control (TFVC) repositories ended on
        April 12, 2024. For more details, see the{' '}
        <a className="Link--inTextBlock" href="https://gh.io/github-importer-non-git-eol">
          changelog
        </a>
        .
      </Flash>

      <Box
        sx={{
          mb: 2,
          pt: 2,
          pb: 3,
          borderBottomStyle: 'solid',
          borderBottomColor: 'border.muted',
          borderBottomWidth: 1,
        }}
      >
        <Heading as="h2" sx={{fontSize: 3, mt: 3, mb: 3}}>
          {sourceRepoHeaderText}
        </Heading>
        <FormControl required>
          <FormControl.Label>{vcsUrlLabel}</FormControl.Label>
          <TextInput
            ref={vcsUrlRef}
            block
            aria-label={vcsUrlText}
            aria-describedby={warningValidationId}
            name={vcsUrlText}
            placeholder={vcsPlaceholderText}
            sx={{my: 1}}
            value={vcsUrl}
            onChange={e => setVcsUrl(e.target.value)}
          />
          {vcsUrl.startsWith('http://') && (
            <WarningValidation id={warningValidationId}>
              This repository will be imported over an unencrypted connection
            </WarningValidation>
          )}
          {unsupportedProtocol && (
            <FormControl.Validation variant="error">
              The {unsupportedProtocol} protocol is not supported
            </FormControl.Validation>
          )}
          {submitClicked && !vcsUrl && (
            <FormControl.Validation variant="error">URL must not be blank</FormControl.Validation>
          )}
          {submitClicked && vcsUrl && !isValidHttpUrl && (
            <FormControl.Validation variant="error">URL must be valid</FormControl.Validation>
          )}
        </FormControl>
        <span>
          Learn more about{' '}
          <Link
            href="https://docs.github.com/get-started/importing-your-projects-to-github/importing-source-code-to-github/about-github-importer"
            target="_blank"
            inline
          >
            importing git repositories
          </Link>
          .
        </span>
      </Box>

      {isGitHubImporterOnActionsEnabled && (
        <Box
          sx={{
            mb: 2,
            pt: 2,
            pb: 3,
            borderBottomStyle: 'solid',
            borderBottomColor: 'border.muted',
            borderBottomWidth: 1,
          }}
        >
          <Text
            as="p"
            sx={{
              fontStyle: 'italic',
              mb: 3,
            }}
          >
            Please enter your credentials if required for cloning your remote repository.
          </Text>
          <FormControl>
            <FormControl.Label>Your username for your source repository</FormControl.Label>
            <TextInput
              block
              aria-label="Your username for your source repository"
              name="Your username for your source repository"
              sx={{my: 1}}
              value={sourceUsername}
              onChange={e => setSourceUsername(e.target.value)}
            />
          </FormControl>
          <FormControl>
            <FormControl.Label>Your access token or password for your source repository</FormControl.Label>
            <TextInput
              block
              aria-label="Your access token or password for your source repository"
              name="Your access token or password for your source repository"
              sx={{my: 1}}
              value={sourceToken}
              onChange={e => setSourceToken(e.target.value)}
              type="password"
            />
          </FormControl>
        </Box>
      )}
      <div>
        <Heading as="h2" sx={{fontSize: 3, mt: 3, mb: 3}}>
          Your new repository details
        </Heading>
        <OwnerRepoVisibilitySelection ref={ownerRepoVisibilitySelectionRef} {...ownerRepoVisibilitySelectionProps} />
        <CreationSummary action="create" owner={ownerSelection} visibility={selectedVisibility} />
      </div>
    </RepoCreationForm>
  )
}

// Note: this components mirrors the warning variant for `FormControl.Validation` from `@primer/react` v35 which is
// expected to be removed in v36. No alternative was found for this specific use-case that would work for the current
// design so this code was inlined as a result.
function WarningValidation({children, id}: React.PropsWithChildren<{id: string}>) {
  return (
    <Text
      aria-live="polite"
      id={id}
      sx={{
        display: 'flex',
        columnGap: 1,
        color: 'attention.fg',
        fontWeight: 'bold',
        fontSize: 0,
        lineHeight: '1.33333',
      }}
    >
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          minHeight: 'calc(12 * 1.33333)',
        }}
      >
        <AlertFillIcon size={12} />
      </Box>
      {children}
    </Text>
  )
}

try{ Import.displayName ||= 'Import' } catch {}
try{ WarningValidation.displayName ||= 'WarningValidation' } catch {}