import {Box, Checkbox, FormControl, Link, Text} from '@primer/react'

import type {Team} from '../types/repo-creation-types'

export function TeamListItem({
  team,
  selectedTeamIds,
  onChange,
}: {
  team: Team
  selectedTeamIds: string[]
  onChange: (s: string) => void
}) {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        padding: 3,
        borderTopWidth: 0,
        borderBottomWidth: 0.5,
        borderLeftWidth: 1,
        borderRightWidth: 1,
        borderStyle: 'solid',
        borderColor: 'border.muted',
        '&:first-child': {borderTopWidth: 1, borderTopLeftRadius: 2, borderTopRightRadius: 2},
        '&:last-child': {borderBottomWidth: 1, borderBottomLeftRadius: 2, borderBottomRightRadius: 2},
      }}
      data-testid="team-list-item"
    >
      <Box sx={{mr: 2}}>
        <FormControl>
          <Checkbox
            checked={selectedTeamIds.includes(team.id)}
            onChange={() => onChange(team.id)}
            value={team.name}
            aria-label={`Team ${team.name}`}
            data-testid="team-checkbox"
          />
          <FormControl.Label visuallyHidden>{team.name}</FormControl.Label>
        </FormControl>
      </Box>
      <Box sx={{flex: 'auto'}}>
        <strong>
          <Link href={team.path}>{team.name}</Link>
        </strong>
        <Text sx={{fontSize: 'small', color: 'fg.muted', display: 'block'}}>{team.description}</Text>
      </Box>
      <Text sx={{mr: 2}}>{team.membersScopeCount}</Text>
      <span>{team.repositoriesScopeCount}</span>
    </Box>
  )
}

try{ TeamListItem.displayName ||= 'TeamListItem' } catch {}