/* eslint eslint-comments/no-use: off */
import {SearchDropdown} from '@github-ui/search-dropdown'
import {Box, FormControl, Link} from '@primer/react'

import {useUserAnalytics} from '../hooks/use-user-analytics'
import type {License} from '../types/repo-creation-types'

export function LicensePicker({
  licenses,
  selectedLicense,
  onLicenseSelection,
  docsUrl,
}: {
  licenses: License[]
  selectedLicense?: License
  onLicenseSelection: (license: License) => void
  docsUrl: string
}) {
  const {sendRepoFormClickEvent} = useUserAnalytics()

  return (
    <>
      <FormControl sx={{mb: 3}}>
        <FormControl.Label id="license-picker-label" htmlFor="license-picker">
          Choose a license
        </FormControl.Label>
        <FormControl.Caption id="license-picker-caption">
          A license tells others what they can and can&apos;t do with your code.{' '}
          <Link inline href={docsUrl} target="_blank">
            Learn more about licenses.
          </Link>
        </FormControl.Caption>
        <Box sx={{my: 1}}>
          <SearchDropdown
            title="License"
            inputLabel="License filter"
            selectedItem={selectedLicense}
            items={licenses}
            onOpen={() =>
              sendRepoFormClickEvent('LICENSE_SELECT', {
                action: 'open',
              })
            }
            onSelect={license => {
              onLicenseSelection(license)
              sendRepoFormClickEvent('LICENSE_SELECT', {
                action: 'select',
                option: license.text,
              })
            }}
            textInputProps={{id: 'license-picker', 'aria-describedby': 'license-picker-caption'}}
            anchorButtonProps={{'aria-describedby': 'license-picker-label'}}
            allowNoneOption={true}
          />
        </Box>
      </FormControl>
    </>
  )
}

try{ LicensePicker.displayName ||= 'LicensePicker' } catch {}