import {useCurrentRepository} from '@github-ui/current-repository'
import {repoForkOwnerItemsPath, repoOwnerItemsPath} from '@github-ui/paths'

import {useCurrentFormType} from '../contexts/CurrentForm'

export function useRepoOwnerItemsPath() {
  const repo = useCurrentRepository()
  const forking = useCurrentFormType() === 'fork'
  const url = forking ? repoForkOwnerItemsPath(repo.id) : repoOwnerItemsPath()
  return url
}
