import {debounce} from '@github/mini-throttle'
import {repositoryCheckNamePath} from '@github-ui/paths'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useCallback, useState} from 'react'

export interface CheckResult {
  status: 'ok' | 'reworded' | 'error' | 'not_found' | 'none'
  repo?: string
  errors?: string
}
interface RepoName {
  owner: string
  name: string
}

/**
 * This hook returns 2 pieces:
 * 1. The result of the check ('none' if not run yet)
 * 2. A function to run the check on demand (and debounced)
 */
type ReturnType = [CheckResult, (newName: RepoName) => void]

/** A hook to check whether the repo name is valid when it changes */
export function useCheckRepoName(onValidityChange: (isValid: boolean) => void): ReturnType {
  const [result, setResult] = useState<CheckResult>({status: 'none'})

  async function fetchCheck({name, owner}: RepoName) {
    const formData = new FormData()
    formData.set('value', name)
    formData.set('owner', owner)
    formData.set('json_response', 'true')

    try {
      const response = await verifiedFetch(repositoryCheckNamePath(), {
        method: 'POST',
        body: formData,
      })
      const {repo, errors} = await response.json()
      if (response.status === 404) {
        setResult({status: 'not_found'})
      } else if (!response.ok) {
        setResult({status: 'error', repo, errors})
      } else {
        setResult({status: repo === name ? 'ok' : 'reworded', repo})
        onValidityChange(true)
        return
      }
    } catch (err) {
      setResult({status: 'error'})
    }

    onValidityChange(false)
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps -- this lint rule wants an inline function, but debounce seems more natural
  const debouncedFetchCheck = useCallback(debounce(fetchCheck, 300), [])

  const runCheck = useCallback(
    (newName: RepoName) => {
      setResult({status: 'none'})
      if (newName.name && newName.owner) {
        debouncedFetchCheck(newName)
      }
    },
    [debouncedFetchCheck],
  )

  return [result, runCheck]
}

export function isError(result: CheckResult): boolean {
  return result.status === 'error' || result.status === 'not_found'
}
