import {useAnalytics} from '@github-ui/use-analytics'
import {useCallback} from 'react'

export type RepositoryClickTarget =
  | 'README_CHECKBOX'
  | 'LICENSE_SELECT'
  | 'GITIGNORE_SELECT'
  | 'TEMPLATE_SELECT'
  | 'ONE_BRANCH_CHECKBOX'
  | 'SUBMIT_BUTTON'

type SendRepoClickEventFunction = (target: RepositoryClickTarget, payload?: {[key: string]: unknown}) => void

/**
 * Use this hook to send user analytics events to the data warehouse.
 * Please note that hook uses `sendEvent` helper,
 * which enriches event context with additional information about the user, repository, and current page.
 *
 *
 * @example
 * ```tsx
 * function Component() {
 *   const { sendRepoClickEvent } = useUserAnalytics()
 *   return <Button onClick={() => sendRepoClickEvent('FIND_FILE_BUTTON')}>Find file</Button>
 * }
 * ```
 *
 */
export function useUserAnalytics(): {
  sendRepoFormClickEvent: SendRepoClickEventFunction
} {
  const {sendAnalyticsEvent} = useAnalytics()

  return {
    sendRepoFormClickEvent: useCallback(
      (target, payload = {}) => {
        sendAnalyticsEvent('repo_form.click', target, payload)
      },
      [sendAnalyticsEvent],
    ),
  }
}
