import {SafeHTMLBox} from '@github-ui/safe-html'
import {SingleSignOnBanner} from '@github-ui/single-sign-on-banner'
import {Box, Flash, Heading, Text} from '@primer/react'
import {useEffect, useRef} from 'react'

export function RepoCreationForm({
  children,
  flash,
  title,
  description,
  protectedOrgLogins,
  submitButton,
  onSubmit,
}: {
  children: React.ReactNode
  flash: string
  title: string
  description: string | React.ReactNode
  protectedOrgLogins: string[]
  submitButton?: React.ReactNode
  onSubmit?: () => void
}) {
  const flashRef = useRef<HTMLDivElement | null>(null)

  useEffect(() => {
    flashRef.current?.focus()
  }, [flash, flashRef])

  return (
    <Box
      as="form"
      // By default HTML5 form will focus on the first required input with missing value on submit.
      // Although, this is fine for simple input fields it doesn't work nicely with custom inputs such as owner selector.
      // In result, it breaks our custom focus order.
      // This property disables default form behavior.
      noValidate
      sx={{my: 6, px: 3}}
      onSubmit={e => {
        e.preventDefault()
        onSubmit?.()
      }}
      className="container-md px-md-4 px-lg-5"
    >
      {flash && (
        <Flash tabIndex={0} ref={flashRef} data-testid="repo-creation-form-error-flash" variant="danger" sx={{mb: 2}}>
          <SafeHTMLBox unverifiedHTML={flash} />
        </Flash>
      )}
      <Box sx={{pb: 2, mb: 2, borderBottom: 'solid', borderBottomColor: 'border.muted', borderBottomWidth: 1}}>
        <Heading as="h1" sx={{fontSize: 4}}>
          {title}
        </Heading>
        <Text sx={{color: 'fg.muted'}}>{description}</Text>
      </Box>
      <Text sx={{fontStyle: 'italic'}}>Required fields are marked with an asterisk (*).</Text>
      <Box sx={{mt: 2}}>
        <SingleSignOnBanner protectedOrgs={protectedOrgLogins} />
      </Box>
      {children}
      <Box sx={{display: 'flex', flexDirection: 'row', justifyContent: 'flex-end'}}>{submitButton}</Box>
    </Box>
  )
}

try{ RepoCreationForm.displayName ||= 'RepoCreationForm' } catch {}