import React from 'react'

type FormType = 'fork' | 'create' | 'transfer'
const CurrentFormTypeContext = React.createContext<FormType>('create')

export function CurrentFormTypeProvider({type = 'create', children}: React.PropsWithChildren<{type?: FormType}>) {
  return <CurrentFormTypeContext.Provider value={type}> {children} </CurrentFormTypeContext.Provider>
}

export function useCurrentFormType() {
  return React.useContext(CurrentFormTypeContext)
}

try{ CurrentFormTypeContext.displayName ||= 'CurrentFormTypeContext' } catch {}
try{ CurrentFormTypeProvider.displayName ||= 'CurrentFormTypeProvider' } catch {}