import {jsonRoute} from '@github-ui/react-core/json-route'
import {registerReactAppFactory} from '@github-ui/react-core/register-app'

import {App} from './App'
import Create from './pages/Create'
import Fork from './pages/Fork'
import Import from './pages/Import'
import Transfer from './pages/Transfer'

registerReactAppFactory('repo-creation', () => ({
  App,
  routes: [
    jsonRoute({path: '/:owner/:repo/transfer', Component: Transfer}),
    jsonRoute({path: '/new/import', Component: Import}),
    jsonRoute({path: '/new', Component: Create}),
    jsonRoute({path: '/organizations/:org/repositories/new', Component: Create}),
    jsonRoute({path: '/:owner/:repo/fork', Component: Fork}),
  ],
}))
