import {useCurrentRepository} from '@github-ui/current-repository'
import type {OwnerItem} from '@github-ui/owner-dropdown'
import {repoOwnerDetailPath} from '@github-ui/paths'
import type {SafeHTMLString} from '@github-ui/safe-html'
import {verifiedFetch} from '@github-ui/verified-fetch'
import {useCallback, useState} from 'react'

import {useCurrentFormType} from '../contexts/CurrentForm'
import type {OwnerSelection} from '../types/repo-creation-types'

export function useSelectedOwner(initial?: OwnerSelection): {
  ownerSelection: OwnerSelection | undefined
  changeOwner: (newOwner: OwnerItem) => void
  errorMessage: string
  fetchingOwnerDetails: boolean
} {
  const [ownerSelection, setOwnerSelection] = useState(initial)
  const [fetchingOwnerDetails, setFetchingOwnerDetails] = useState(false)
  const [errorMessage, setErrorMessage] = useState('')
  const repoId = useCurrentRepository()?.id
  const formType = useCurrentFormType()

  const changeOwner = useCallback(
    async (newOwner: OwnerItem) => {
      setErrorMessage('')
      setFetchingOwnerDetails(true)
      try {
        const path =
          formType === 'transfer' && repoId
            ? repoOwnerDetailPath(newOwner.name, 'transfer', repoId)
            : repoOwnerDetailPath(newOwner.name)

        const result = await verifiedFetch(path)
        if (!result.ok) {
          setErrorMessage(`Server error loading owner: ${result.status}`)
          setFetchingOwnerDetails(false)
          return
        }

        const data = await result.json()
        setOwnerSelection(data)
      } catch (error) {
        setErrorMessage('Network error loading owner')
      }
      setFetchingOwnerDetails(false)
    },
    [repoId, formType],
  )

  return {ownerSelection, changeOwner, errorMessage: errorMessage as SafeHTMLString, fetchingOwnerDetails}
}
