import {useCurrentRepository} from '@github-ui/current-repository'
import {
  type PropertyField,
  propertyFieldsToChangedPropertiesMap,
  useEditCustomProperties,
} from '@github-ui/custom-properties-editing/use-edit-custom-properties'
import type {PropertyValuesRecord} from '@github-ui/custom-properties-types'
import {repoAbortTransferPath, repoTransferPath} from '@github-ui/paths'
import {useFeatureFlag} from '@github-ui/react-core/use-feature-flag'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import sudo from '@github-ui/sudo'
import {useNavigate} from '@github-ui/use-navigate'
import {verifiedFetch, verifiedFetchJSON} from '@github-ui/verified-fetch'
import {AlertIcon} from '@primer/octicons-react'
import {Box, Button, Flash, FormControl, Link, Octicon, Text, TextInput} from '@primer/react'
import {useMemo, useRef, useState} from 'react'

import {OwnerRadioButtonSelection} from '../components/OwnerRadioButtonSelection'
import {RepoCreationForm} from '../components/RepoCreationForm'
import {RepoNameInput} from '../components/RepoNameInput'
import {RequiredCustomPropertiesSelection} from '../components/RequiredCustomPropertiesSelection'
import {TeamSelection} from '../components/TeamSelection'
import {CurrentFormTypeProvider} from '../contexts/CurrentForm'
import {useSelectedOwner} from '../hooks/use-selected-owner'
import {
  type OwnerSelection,
  type Team,
  TransferOwnerInputType,
  type TransferRepoPayload,
} from '../types/repo-creation-types'

interface RepoTransferBody {
  new_owner: string
  new_name: string
  custom_properties?: PropertyValuesRecord
}

export default function Transfer() {
  return (
    <CurrentFormTypeProvider type="transfer">
      <TransferForm />
    </CurrentFormTypeProvider>
  )
}

function TransferForm() {
  const repo = useCurrentRepository()
  const {
    repoCreate,
    pricingPath,
    repoNonTransferrableReason,
    repositoryTransferRequestsEnabled,
    brandedPlanName,
    pluralizedCodespaces,
    hasPage,
    codespaces,
    ownerFreePlan,
    organizationDiscussion,
    pendingTransfer,
    canTransferOwnership,
    hasRulesetsOrProtectedBranches,
  } = useRoutePayload<TransferRepoPayload>()
  const propertiesOverlayEditorEnabled = useFeatureFlag('custom_properties_edit_modal')
  const {ownerItems, protectedOrgLogins} = repoCreate

  const [isCorrectConfirmation, setIsCorrectConfirmation] = useState(false)
  const [teams, setTeams] = useState<Team[]>([])
  const [teamPageCount, setTeamPageCount] = useState(0)
  const [submitClicked, setSubmitClicked] = useState(false)

  const {ownerSelection, changeOwner, errorMessage} = useSelectedOwner()

  const {propertyValuesMap, setPropertyValue, revertPropertyValue} = useEditCustomProperties(
    [{}],
    ownerSelection?.requiredDefinitions || [],
    [ownerSelection?.name],
  )

  const [currentFreeformInput, setCurrentFreeformInput] = useState('')
  const [repositoryName, setRepositoryName] = useState(repo.name)
  const [selectedRadio, setSelectedRadio] = useState<TransferOwnerInputType>(
    ownerItems?.length === 0 ? TransferOwnerInputType.FreeformInput : TransferOwnerInputType.ExistingOrgs,
  )
  const [flash, setFlash] = useState('')
  const [isValidRepoName, setIsValidRepoName] = useState(true)
  const [isTransferring, setIsTransferring] = useState(false)
  const [isAborting, setIsAborting] = useState(false)

  const ownerRadioButtonSelectionRef = useRef<OwnerRadioButtonSelectionRef>()
  const repoInputRef = useRef<HTMLInputElement>(null)
  const confirmationInputRef = useRef<HTMLInputElement>(null)

  const navigate = useNavigate()

  const {canRename, newOwner} = (() => {
    switch (selectedRadio) {
      case TransferOwnerInputType.ExistingOrgs:
        return {newOwner: ownerSelection?.name || '', canRename: true}
      case TransferOwnerInputType.FreeformInput:
        return {newOwner: currentFreeformInput, canRename: false}
      default:
        return {newOwner: '', canRename: false}
    }
  })()

  const nameWithOwner = `${repo.ownerLogin}/${repo.name}`
  const newRepoNameToSubmit = canRename ? repositoryName : repo.name

  const transferHandler = async () => {
    setSubmitClicked(true)

    if (isTransferring) return

    const propertiesList = Object.values(propertyValuesMap)
    const invalidPropertyValues = propertiesList.some(p => !!p.error)

    if (isCorrectConfirmation && newOwner && isValidRepoName && !invalidPropertyValues) {
      setIsTransferring(true)
      if (await sudo()) {
        const body: RepoTransferBody = {
          ['new_owner']: newOwner,
          ['new_name']: newRepoNameToSubmit,
        }

        const formData = new FormData()
        formData.append('new_owner', newOwner)
        formData.append('new_name', newRepoNameToSubmit)

        const selectedProperties = getSelectedCustomProperties({owner: ownerSelection, propertyValuesMap})
        if (selectedProperties) {
          body['custom_properties'] = selectedProperties
        }

        try {
          const result = await verifiedFetchJSON(repoTransferPath(repo), {
            method: 'POST',
            body,
          })

          if (result.redirected) {
            navigate(result.url)
          } else {
            const json = await result.json()
            if (!result.ok) {
              setFlash(json.data.error)
            } else if (json.data.teams) {
              setTeams(json.data.teams)
              setTeamPageCount(json.data.pageCount)
            } else if (json.data.redirect) {
              navigate(json.data.redirect)
            } else {
              setFlash('Something unexpected went wrong!')
            }
          }
        } catch (e) {
          setFlash('Something went wrong!')
        }
      }
      setIsTransferring(false)
    } else if (!newOwner) {
      if (selectedRadio === TransferOwnerInputType.ExistingOrgs) {
        ownerRadioButtonSelectionRef?.current?.focusOwnerDropdown()
        return
      } else if (selectedRadio === TransferOwnerInputType.FreeformInput) {
        ownerRadioButtonSelectionRef?.current?.focusFreeformOwnerInput()
        return
      }
    } else if (!isValidRepoName) {
      repoInputRef.current?.focus()
      return
    } else {
      confirmationInputRef.current?.focus()
      return
    }
  }

  const canEditPropertyValues = !!ownerSelection?.editableDefinitionNames?.length
  const displayRequiredCustomProperties = selectedRadio === TransferOwnerInputType.ExistingOrgs && canEditPropertyValues

  const editableDefinitions = useMemo(() => {
    if (!ownerSelection) return []

    return ownerSelection.requiredDefinitions.filter(definition =>
      ownerSelection.editableDefinitionNames.includes(definition.propertyName),
    )
  }, [ownerSelection])

  const transferContent = () => {
    if (pendingTransfer) {
      const abortHandler = async () => {
        setIsAborting(true)
        if (await sudo()) {
          try {
            const result = await verifiedFetch(repoAbortTransferPath(repo), {
              method: 'POST',
              headers: {Accept: 'text/html'},
            })
            if (result.redirected) {
              navigate(result.url)
            } else {
              setFlash('Something went wrong while aborting transfer!')
            }
          } catch (e) {
            setFlash('Something went wrong while aborting transfer!')
          }
        }
      }
      return {
        formContent: (
          <Text
            as="p"
            key="content"
            sx={{
              pt: 2,
              pb: 3,
              mb: 3,
              borderBottomColor: 'border.muted',
              borderBottomWidth: 1,
              borderBottomStyle: 'solid',
            }}
          >
            This repository is being transferred to <strong>@{pendingTransfer}.</strong>
          </Text>
        ),
        buttonContent: (
          <Button key="button" type="submit" variant="danger" disabled={isAborting}>
            {isAborting ? 'Aborting transfer...' : 'Abort transfer'}
          </Button>
        ),
        onSubmit: abortHandler,
      }
    } else if (canTransferOwnership) {
      return {
        formContent: (
          <div key="content">
            <Box
              sx={{
                mb: 1,
                pb: 3,
                borderBottomColor: 'border.muted',
                borderBottomWidth: 1,
                borderBottomStyle: 'solid',
              }}
            >
              <section aria-label="Further information on repository transfers">
                <Flash variant="warning" sx={{mt: 3}}>
                  <Octicon icon={AlertIcon} />
                  To understand admin access, teams, issue assignments, and redirects after a repository is transferred,
                  see{' '}
                  <Link
                    inline
                    href="https://docs.github.com/repositories/creating-and-managing-repositories/transferring-a-repository"
                  >
                    Transferring a repository
                  </Link>{' '}
                  in GitHub Help.
                </Flash>
              </section>
              {repositoryTransferRequestsEnabled && (
                <Text sx={{mt: 3, display: 'inline-block'}}>
                  Transferring may be delayed until the new owner approves the transfer.
                </Text>
              )}
              {repositoryTransferRequestsEnabled && !!codespaces && (
                <span>
                  {' '}
                  There {codespaces === 1 ? 'is' : 'are'} {pluralizedCodespaces} that may be affected by this transfer.
                </span>
              )}
            </Box>
            <Box
              data-hpc
              sx={{
                pt: 2,
                pb: 2,
                mb: 3,
                borderBottomColor: 'border.muted',
                borderBottomWidth: 1,
                borderBottomStyle: 'solid',
              }}
            >
              <OwnerRadioButtonSelection
                ref={ownerRadioButtonSelectionRef}
                ownerItems={ownerItems}
                excludedOrg={repo.ownerLogin}
                hideSelectOwnerCheck={!submitClicked}
                onRadioChange={setSelectedRadio}
                selectedRadio={selectedRadio}
                selectedOwner={ownerSelection}
                onCurrentDropdownSelectionChange={changeOwner}
                currentFreeformInput={currentFreeformInput}
                onCurrentFreeformInputChange={setCurrentFreeformInput}
                ownerValidationError={submitClicked && newOwner === '' ? 'New owner must not be blank' : ''}
              />
              {newOwner && canRename && (
                <RepoNameInput
                  ref={repoInputRef}
                  repositoryName={repositoryName}
                  selectedOwnerName={newOwner}
                  onChange={setRepositoryName}
                  onValidityChange={setIsValidRepoName}
                />
              )}
            </Box>
            {organizationDiscussion && (
              <Text sx={{mt: 2}}>
                This will disable {repo.public ? 'public' : 'member'} organization discussions for{' '}
                <strong>{repo.ownerLogin}</strong>.
              </Text>
            )}
            {!ownerFreePlan && repo.private && (
              <FreeAndPrivateWarning
                {...{
                  hasPage,
                  codespaces,
                  newOwner,
                  brandedPlanName,
                  pricingPath,
                }}
              />
            )}
            {hasRulesetsOrProtectedBranches && <RulesetsAndProtectedBranchesBypassableActorsWarning />}
            {displayRequiredCustomProperties && (
              <Box
                sx={{
                  borderBottomStyle: 'solid',
                  borderBottomColor: 'border.muted',
                  borderBottomWidth: 1,
                  pb: 3,
                  mt: 3,
                }}
              >
                <RequiredCustomPropertiesSelection
                  definitions={ownerSelection.requiredDefinitions}
                  editableDefinitions={editableDefinitions}
                  propertyValuesMap={propertyValuesMap}
                  setPropertyValue={setPropertyValue}
                  revertPropertyValue={revertPropertyValue}
                  orgName={ownerSelection.name}
                  propertiesOverlayEditorEnabled={propertiesOverlayEditorEnabled}
                />
              </Box>
            )}
            {((!ownerFreePlan && repo.private) || hasRulesetsOrProtectedBranches) && (
              <Flash variant="warning" sx={{mt: 4}}>
                <Octicon icon={AlertIcon} />
                Warning: This is a potentially destructive action.
              </Flash>
            )}
            <Box sx={{mt: 3}}>
              <FormControl required sx={{mb: 3}}>
                <FormControl.Label>
                  Type <b>{nameWithOwner}</b> to confirm.
                </FormControl.Label>
                <TextInput
                  block
                  ref={confirmationInputRef}
                  data-testid="transfer-repo-confirmation"
                  aria-label={`Type ${nameWithOwner} to confirm`}
                  name={`Type ${nameWithOwner} to confirm`}
                  onChange={e => setIsCorrectConfirmation(e.target.value === nameWithOwner)}
                />
                {submitClicked && !isCorrectConfirmation && (
                  <FormControl.Validation variant="error">Must match {nameWithOwner}</FormControl.Validation>
                )}
              </FormControl>
            </Box>
          </div>
        ),
        buttonContent: (
          <Button key="button" type="submit" variant="danger" disabled={isTransferring}>
            {isTransferring ? 'Transferring repository' : 'I understand, transfer this repository.'}
          </Button>
        ),
        onSubmit: transferHandler,
      }
    } else {
      return {
        formContent: <Text sx={{mb: 0}}>{repoNonTransferrableReason}</Text>,
      }
    }
  }

  const {formContent, buttonContent, onSubmit} = transferContent()

  let selectedProperties
  if (teamPageCount) {
    selectedProperties = getSelectedCustomProperties({owner: ownerSelection, propertyValuesMap})
  }

  return teamPageCount ? (
    <TeamSelection
      teams={teams ? teams : []}
      initialPageCount={teamPageCount}
      onTeamFetch={setTeams}
      newOwner={newOwner}
      repositoryName={repositoryName}
      errorHandler={() => setFlash('Something unexpected went wrong')}
      selectedProperties={selectedProperties}
    />
  ) : (
    <RepoCreationForm
      flash={flash || errorMessage}
      title={`Transfer repository: ${nameWithOwner}`}
      description="Transfer this repository to another user or to an organization where you have the ability to create repositories."
      protectedOrgLogins={protectedOrgLogins}
      onSubmit={onSubmit}
      submitButton={buttonContent}
    >
      {formContent}
    </RepoCreationForm>
  )
}

function FreeAndPrivateWarning({
  hasPage,
  codespaces,
  newOwner,
  brandedPlanName,
  pricingPath,
}: {
  hasPage: boolean
  codespaces: number
  newOwner: string
  brandedPlanName: string
  pricingPath: string
}) {
  const syncedNewOwner = (
    <strong>
      @<span>{newOwner || 'username'}</span>
    </strong>
  )
  return (
    <Box
      sx={{
        mb: 3,
        borderBottomColor: 'border.muted',
        borderBottomWidth: 1,
        borderBottomStyle: 'solid',
      }}
    >
      <Text as="p" sx={{mb: 2}}>
        If {syncedNewOwner} is using {brandedPlanName} and accepts the transfer, they will lose access to private
        repository features:
      </Text>
      <ul className="ml-4 mb-2">
        <li className="mb-2">
          <strong>Code owners</strong>
        </li>
        <li className="mb-2">
          Any existing <strong>wikis</strong>
        </li>
        <li className="mb-2">
          <strong>Pulse, Contributors, Community, Traffic, Commits, Code Frequency, Network,</strong> and{' '}
          <strong>Forks</strong> on the <strong>Insights</strong> tab
        </li>
        <li className="mb-2">
          <strong>Draft</strong> PRs
        </li>
        <li className="mb-2">
          <strong>Multiple assignees</strong> for issues and PRs
        </li>
        <li className="mb-2">
          <strong>Multiple reviewers</strong> for PRs
        </li>
        <li className="mb-2">
          <strong>Branch and tag protection rules</strong>
        </li>
        {hasPage && (
          <li>
            <strong>GitHub Pages&mdash;</strong>
            published GitHub Pages will be disabled
          </li>
        )}
        {codespaces > 0 && (
          <li>
            <strong>Codespaces</strong> will be disabled
          </li>
        )}
      </ul>
      <Text as="p" sx={{mb: 3}}>
        {syncedNewOwner} can {pricingPath ? <Link href={pricingPath}>upgrade</Link> : 'upgrade'} their plan before
        accepting the transfer to avoid losing access.
      </Text>
    </Box>
  )
}

function RulesetsAndProtectedBranchesBypassableActorsWarning() {
  return (
    <Box
      sx={{
        mb: 3,
        borderBottomColor: 'border.muted',
        borderBottomWidth: 1,
        borderBottomStyle: 'solid',
      }}
    >
      <Text as="p" sx={{mb: 2}}>
        Individual users, teams, and apps will be removed from the following options:
      </Text>
      <ul className="ml-4 mb-2">
        <li className="mb-2">
          <strong>Repository ruleset bypassers</strong>
        </li>
        <li className="mb-2">
          <strong>Protected branch pull request bypassers</strong>
        </li>
        <li className="mb-2">
          <strong>Protected branch authorized pull request review dismissers</strong>
        </li>
        <li className="mb-2">
          <strong>Protected branch authorized pushers</strong>
        </li>
        <li className="mb-3">
          <strong>Protected branch allowed force pushers</strong>
        </li>
      </ul>
    </Box>
  )
}

interface OwnerRadioButtonSelectionRef {
  focusOwnerDropdown: () => void
  focusFreeformOwnerInput: () => void
}

function getSelectedCustomProperties({
  owner,
  propertyValuesMap,
}: {
  owner?: OwnerSelection
  propertyValuesMap: Record<string, PropertyField>
}) {
  const canEditPropertyValues = !!owner?.editableDefinitionNames?.length
  if (canEditPropertyValues) {
    const changedProperties = propertyFieldsToChangedPropertiesMap(owner.requiredDefinitions, propertyValuesMap)
    return Object.keys(changedProperties).length ? changedProperties : undefined
  }
}

try{ Transfer.displayName ||= 'Transfer' } catch {}
try{ TransferForm.displayName ||= 'TransferForm' } catch {}
try{ FreeAndPrivateWarning.displayName ||= 'FreeAndPrivateWarning' } catch {}
try{ RulesetsAndProtectedBranchesBypassableActorsWarning.displayName ||= 'RulesetsAndProtectedBranchesBypassableActorsWarning' } catch {}