import {CurrentRepositoryProvider, type Repository} from '@github-ui/current-repository'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {PropsWithChildren} from 'react'

export function App({children}: PropsWithChildren) {
  const {repo} = useRoutePayload<{repo?: Repository}>()
  return repo ? <CurrentRepositoryProvider repository={repo}>{children}</CurrentRepositoryProvider> : <>{children}</>
}

try{ App.displayName ||= 'App' } catch {}