import {testIdProps} from '@github-ui/test-id-props'
import {Box, FormControl, TextInput} from '@primer/react'
import {forwardRef, useEffect} from 'react'

import {type CheckResult, isError, useCheckRepoName} from '../hooks/check-repo-name'

const repoInputAvailableMessageId = 'RepoNameInput-is-available'
const repoInputDefaultMessageId = 'RepoNameInput-message'

export const RepoNameInput = forwardRef(
  (
    props: {
      repositoryName: string
      readonly?: boolean
      hideBlankCheck?: boolean
      selectedOwnerName: string | undefined
      onChange: (newRepoName: string) => void
      onValidityChange: (valid: boolean) => void
    },
    ref: React.ForwardedRef<HTMLInputElement>,
  ) => {
    const {repositoryName, selectedOwnerName, onChange, onValidityChange} = props
    const [result, runCheck] = useCheckRepoName(onValidityChange)

    useEffect(() => {
      onValidityChange(false)
      if (selectedOwnerName) {
        runCheck({name: repositoryName, owner: selectedOwnerName})
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps -- Only re-check when owner or repo change
    }, [selectedOwnerName, repositoryName])

    return (
      <FormControl required>
        <FormControl.Label>Repository name</FormControl.Label>
        <TextInput
          ref={ref}
          value={repositoryName}
          onChange={event => onChange(event.target.value)}
          readOnly={props.readonly}
          validationStatus={isError(result) ? 'error' : undefined}
          aria-describedby={`${repoInputAvailableMessageId} ${repoInputDefaultMessageId}`}
          {...testIdProps('repository-name-input')}
        />
        <NameCheckValidation
          result={result}
          repoName={repositoryName}
          ownerName={selectedOwnerName}
          hideBlankCheck={props.hideBlankCheck}
        />
      </FormControl>
    )
  },
)

RepoNameInput.displayName = 'RepoNameInput'

interface NameCheckValidationProps {
  ownerName?: string
  repoName: string
  hideBlankCheck?: boolean
  result: CheckResult
}

function NameCheckValidation(props: NameCheckValidationProps) {
  const {ownerName, repoName, result} = props
  if (!repoName) {
    return props.hideBlankCheck ? null : (
      <FormControl.Validation id={repoInputDefaultMessageId} variant="error">
        New repository name must not be blank
      </FormControl.Validation>
    )
  }

  if (!ownerName) {
    return null
  }

  switch (result.status) {
    case 'none':
      return (
        // This component is a descendant of `FormControl`
        // eslint-disable-next-line primer-react/direct-slot-children
        <FormControl.Caption>
          <span aria-live="polite">Checking availability</span>
        </FormControl.Caption>
      )
    case 'not_found':
      return (
        <FormControl.Validation id={repoInputDefaultMessageId} variant="error">
          Not Found
        </FormControl.Validation>
      )
    case 'error':
      return result.repo ? (
        <FormControl.Validation id={repoInputDefaultMessageId} variant="error">
          The repository <strong>{result.repo}</strong> {result.errors}.
        </FormControl.Validation>
      ) : (
        // An error without repo is not coming from the server, it's likely a fetch error
        <FormControl.Validation id={repoInputDefaultMessageId} variant="error">
          {"Couldn't check availability"}
        </FormControl.Validation>
      )
    case 'reworded':
      return (
        <FormControl.Validation id={repoInputDefaultMessageId} variant="success">
          Your new repository will be created as <strong>{result.repo}</strong>.
          <Box sx={{color: 'fg.muted', fontWeight: 'normal'}}>
            {' '}
            The repository name can only contain ASCII letters, digits, and the characters <code>.</code>,{' '}
            <code>-</code>, and <code>_</code>.
            {repoName.endsWith('.git') && (
              <div>
                {' '}
                The <code>.git</code> extension will be removed.
              </div>
            )}
          </Box>
        </FormControl.Validation>
      )
    case 'ok':
      return (
        <FormControl.Validation id={repoInputAvailableMessageId} variant="success">
          {result.repo} is available.
        </FormControl.Validation>
      )
  }
}

try{ NameCheckValidation.displayName ||= 'NameCheckValidation' } catch {}