import type {PropertyDefinition} from '@github-ui/custom-properties-types'
import type {OwnerItem} from '@github-ui/owner-dropdown'
import type {Visibility} from '@github-ui/repos-list/repos-list-types'
import type {SafeHTMLString} from '@github-ui/safe-html'

export interface RepoCreatePayload {
  ownerItems?: OwnerItem[]
  protectedOrgLogins: string[]
  restrictCreateRepositoriesInPersonalNamespace: boolean
  helpUrl: string
  docsUrls: {
    licensePicker: string
  }
}

export interface TransferRepoPayload {
  repoCreate: RepoCreatePayload
  pricingPath: string
  repoNonTransferrableReason: string | null
  repositoryTransferRequestsEnabled: boolean
  brandedPlanName: string
  pluralizedCodespaces: string
  hasPage: boolean
  codespaces: number
  ownerFreePlan: boolean
  organizationDiscussion: boolean
  pendingTransfer: string | null
  canTransferOwnership: boolean
  hasRulesetsOrProtectedBranches: boolean
}

export interface ImportRepoPayload {
  repoCreate: RepoCreatePayload
  initialOwnerSelection?: OwnerSelection
  privateModeEnabled: boolean
  tradeControlsPrivateRepoCreationWarning: SafeHTMLString
  tradeControlsUserPrivateRepoCreationWarning: SafeHTMLString
}

export interface NewRepoPayload {
  repoCreate: RepoCreatePayload
  initialOwnerSelection?: OwnerSelection
  currentUserLogin: string
  suggestedRepoName: string
  privateModeEnabled: boolean
  isGitHubEnterprise: boolean
  tradeControlsPrivateRepoCreationWarning: SafeHTMLString
  tradeControlsUserPrivateRepoCreationWarning: SafeHTMLString
  hasTemplateRepos: boolean
  selectedTemplate?: TemplateRepo | null
  licenses: License[]
  publicReposAvailable: boolean
  repositoryImportLinkDataAttributes?: {[key: string]: string}
  helpUrl: string
  nameIsRestricted: boolean
}

export interface ForkRepoPayload {
  repoCreate: RepoCreatePayload
  initialOwnerSelection?: OwnerSelection
  repoHasForks: boolean
  repoDescription?: string
}

interface Fork {
  name: string
  ownerLogin: string
}

export interface ForkRepoListPayload {
  existingForks: Fork[]
  truncated: boolean
}

export interface TemplateRepo {
  id: number
  nameWithDisplayOwner: string
  ownerDisplayLogin: string
  avatarUrl: string
}

export interface License {
  id: string
  text: string
}

export interface OwnerSelection extends OwnerItem {
  displayedVisibilities: Visibility[]
  defaultVisibility: Visibility
  publicRestrictedByPolicy: boolean
  internalRestrictedByPolicy: boolean
  privateRestrictedByPolicy: boolean
  privateRestrictedByPlan: boolean
  hasTradeRestrictions: boolean
  adminableByCurrentUser: boolean
  defaultNewRepoBranch: string
  defaultBranchSettingsUrl: string | null
  enterpriseManagedUserEnabled: boolean
  installableApps: MarketplaceAppListing[]
  requiredDefinitions: PropertyDefinition[]
  editableDefinitionNames: string[]
}

export enum TransferOwnerInputType {
  ExistingOrgs = 'existing-organizations',
  FreeformInput = 'freeform-input',
}

export interface Team {
  id: string
  name: string
  path: string
  description: string
  membersScopeCount: string
  repositoriesScopeCount: string
}

export interface MarketplaceAppListing {
  id: number
  // Note that bgcolor must be a hex code
  bgcolor: string
  primaryAvatarUrl: string
  name: string
  normalizedShortDescription: string
  autoInstall: boolean
}

export type ValueType = 'string' | 'single_select'
