import {Box, Checkbox, CheckboxGroup, FormControl, Text} from '@primer/react'

import type {MarketplaceAppListing} from '../types/repo-creation-types'

export function MarketplaceAppsSelection({
  listings,
  selectedListings,
  currentOwnerName,
  currentUserLogin,
  handleChange,
}: {
  listings: MarketplaceAppListing[]
  selectedListings: string[]
  currentOwnerName: string
  currentUserLogin: string
  handleChange: (selectedValues: string[]) => void
}) {
  return listings?.length > 0 ? (
    <CheckboxGroup onChange={handleChange}>
      <CheckboxGroup.Label>Grant your Marketplace apps access to this repository</CheckboxGroup.Label>
      <CheckboxGroup.Caption>
        {currentOwnerName === currentUserLogin ? 'You are' : `${currentOwnerName} is`} subscribed to {listings.length}{' '}
        Marketplace app{listings?.length === 1 ? '' : 's'}.
      </CheckboxGroup.Caption>
      {listings.map(listing => (
        <FormControl key={listing.id}>
          <Checkbox
            value={listing.id.toString()}
            checked={listing.autoInstall || selectedListings.includes(listing.id.toString())}
          />
          <FormControl.LeadingVisual>
            <Box className="CircleBadge CircleBadge--tiny" sx={{background: `#${listing.bgcolor}`}}>
              <img className="CircleBadge-icon" src={listing.primaryAvatarUrl} alt={`${listing.name} logo preview`} />
            </Box>
          </FormControl.LeadingVisual>
          <FormControl.Label>
            {listing.name}
            {listing.autoInstall && (
              <Text as="em" sx={{fontSize: 12, fontWeight: 'normal', color: 'fg.muted'}}>
                {' '}
                (auto-installed)
              </Text>
            )}
          </FormControl.Label>
          <FormControl.Caption>{listing.normalizedShortDescription}</FormControl.Caption>
        </FormControl>
      ))}
    </CheckboxGroup>
  ) : null
}

try{ MarketplaceAppsSelection.displayName ||= 'MarketplaceAppsSelection' } catch {}