import {AnalyticsProvider} from '@github-ui/analytics-provider'
import {
  propertyFieldsToChangedPropertiesMap,
  useEditCustomProperties,
} from '@github-ui/custom-properties-editing/use-edit-custom-properties'
import type {PropertyValuesRecord} from '@github-ui/custom-properties-types'
import {repositoriesPath, repositoryImportsPath} from '@github-ui/paths'
import {useFeatureFlag} from '@github-ui/react-core/use-feature-flag'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {Visibility} from '@github-ui/repos-list/repos-list-types'
import {useNavigate, useSearchParams} from '@github-ui/use-navigate'
import {verifiedFetchJSON} from '@github-ui/verified-fetch'
import {Box, Button, Flash, Link} from '@primer/react'
import {type ComponentProps, useEffect, useMemo, useRef, useState} from 'react'

import {CreationSummary} from '../components/CreationSummary'
import {Description} from '../components/Description'
import {DisabledCreationInPersonalNamespaceMessage} from '../components/InfoMessage'
import {InitializeRepositoryOptions} from '../components/InitializeRepositoryOptions'
import {MarketplaceAppsSelection} from '../components/MarketplaceAppsSelection'
import type {OwnerRepoSelectionRef} from '../components/OwnerRepoSelection'
import {OwnerRepoVisibilitySelection} from '../components/OwnerRepoVisibilitySelection'
import {RepoCreationForm} from '../components/RepoCreationForm'
import {RepoNameGenerator} from '../components/RepoNameGenerator'
import {RepoTemplateMenu} from '../components/RepoTemplateMenu'
import {RequiredCustomPropertiesSelection} from '../components/RequiredCustomPropertiesSelection'
import {useSelectedOwner} from '../hooks/use-selected-owner'
import {useUserAnalytics} from '../hooks/use-user-analytics'
import type {License, NewRepoPayload, OwnerSelection} from '../types/repo-creation-types'

interface RepoCreateBody {
  owner: string
  ['template_repository_id']: string
  ['include_all_branches']: '0' | '1'
  repository: {
    name: string
    visibility: Visibility
    description: string
    ['auto_init']: '1' | '0'
    ['license_template']: string
    ['gitignore_template']: string
  }
  ['quick_install']?: Record<string, Record<string, 'on'>>
  ['custom_properties']?: PropertyValuesRecord
}

const INITIAL_README = false
const INITIAL_GITIGNORE = ''
const INITIAL_LICENSE = undefined

export default function Create() {
  const appName = 'repo-creation'
  const category = 'create'
  const metadata = useMemo(() => ({}), [])

  return (
    <AnalyticsProvider appName={appName} category={category} metadata={metadata}>
      <CreateForm />
    </AnalyticsProvider>
  )
}

function CreateForm() {
  const {
    repoCreate,
    initialOwnerSelection,
    currentUserLogin,
    privateModeEnabled,
    isGitHubEnterprise,
    tradeControlsPrivateRepoCreationWarning,
    tradeControlsUserPrivateRepoCreationWarning,
    licenses,
    suggestedRepoName,
    hasTemplateRepos,
    selectedTemplate,
    publicReposAvailable,
    repositoryImportLinkDataAttributes,
    nameIsRestricted,
  } = useRoutePayload<NewRepoPayload>()
  const propertiesOverlayEditorEnabled = useFeatureFlag('custom_properties_edit_modal')
  const [searchParams] = useSearchParams()

  const {ownerItems, restrictCreateRepositoriesInPersonalNamespace, protectedOrgLogins, helpUrl, docsUrls} = repoCreate

  const {ownerSelection, changeOwner, errorMessage, fetchingOwnerDetails} = useSelectedOwner(initialOwnerSelection)
  const [repositoryName, setRepositoryName] = useState(searchParams.get('name') || '')
  const [description, setDescription] = useState(searchParams.get('description') || '')
  const [descriptionTooLong, setDescriptionTooLong] = useState(false)

  const [selectedTemplateRepo, setSelectedTemplateRepo] = useState(selectedTemplate || undefined)

  const [includeAllBranches, setIncludeAllBranches] = useState(false)
  const [addReadme, setAddReadme] = useState(INITIAL_README)
  const [selectedGitignoreTemplate, setSelectedGitignoreTemplate] = useState<string>(INITIAL_GITIGNORE)
  const [selectedLicense, setSelectedLicense] = useState<License | undefined>(INITIAL_LICENSE)
  const [selectedMarketplaceApps, setSelectedMarketplaceApps] = useState<string[]>([])
  const {propertyValuesMap, setPropertyValue, revertPropertyValue} = useEditCustomProperties(
    [{}],
    ownerSelection?.requiredDefinitions || [],
    [ownerSelection?.name],
  )
  const [repoNameChanged, setRepoNameChanged] = useState(false)
  // Initialize to false because repo name is initially blank
  const [isValidRepoName, setIsValidRepoName] = useState(false)
  const [submitClicked, setSubmitClicked] = useState(false)
  const [isCreating, setIsCreating] = useState(false)

  const [readmeParamOverride, setReadmeParamOverride] = useState<Visibility | null>(null)
  const {sendRepoFormClickEvent} = useUserAnalytics()

  useEffect(() => {
    const readmeParam = searchParams.get('profile_readme')
    // `readmeParam` can be set explicitly to 'member', in order to seed the organization config repo name
    // and private visibility for an org member profile.
    // Or, it can be set to 'public', '1', or a truthy value, in order to seed the organization config repo
    // name and public visibility for an org profile. The existing Rails implementation only requires truthiness
    // for this case, not a specific value:
    // https://github.com/github/github/blob/master/app/assets/modules/github/repositories/new.ts#L307
    if (readmeParam === 'member') {
      if (ownerSelection && ownerSelection.isOrganization) {
        setRepositoryName('.github-private')
      }
      setReadmeParamOverride('private')
    } else if (readmeParam) {
      if (ownerSelection) {
        if (ownerSelection.isOrganization) {
          setRepositoryName('.github')
        } else {
          setRepositoryName(ownerSelection.name)
        }
      }
      setReadmeParamOverride('public')
    }
  }, [ownerSelection, searchParams])

  const [createError, setCreateError] = useState('')

  const ownerRepoVisibilitySelectionRef = useRef<OwnerRepoSelectionRef>()

  const navigate = useNavigate()

  // The default visibility is 'private' if no owner is selected, per:
  // app/components/repositories/new/owner_empty_selection_option.rb#L1
  const [userSelectedVisibility, setUserSelectedVisibility] = useState<Visibility>()

  const selectedVisibility =
    userSelectedVisibility ||
    readmeParamOverride ||
    (searchParams.get('visibility') as Visibility) ||
    ownerSelection?.defaultVisibility ||
    'private'

  const createHandler = async () => {
    setSubmitClicked(true)
    sendRepoFormClickEvent('SUBMIT_BUTTON', {
      action: 'submit',
    })

    if (isCreating || fetchingOwnerDetails) return

    const invalidPropertyValues = Object.values(propertyValuesMap).some(p => !!p.error)

    if (isValidRepoName && ownerSelection && !descriptionTooLong && !invalidPropertyValues) {
      const manuallyInstalledSelectedMarketplaceApps = ownerSelection.installableApps
        .filter(x => !x.autoInstall && selectedMarketplaceApps.includes(x.id.toString()))
        .map(x => x.id)

      const body: RepoCreateBody = {
        owner: ownerSelection.name,
        ['template_repository_id']: selectedTemplateRepo?.id.toString() || '',
        ['include_all_branches']: includeAllBranches ? '1' : '0',
        repository: {
          name: repositoryName,
          visibility: selectedVisibility,
          description,
          ['auto_init']: addReadme ? '1' : '0',
          ['license_template']: selectedLicense?.id || '',
          ['gitignore_template']: selectedGitignoreTemplate,
        },
      }

      const apps = manuallyInstalledSelectedMarketplaceApps.reduce<Record<string, 'on'>>((acc, app) => {
        acc[app] = 'on'
        return acc
      }, {})
      if (Object.values(apps).length) {
        body['quick_install'] = {[ownerSelection.name]: apps}
      }

      const canEditPropertyValues = ownerSelection?.editableDefinitionNames.length > 0
      if (canEditPropertyValues) {
        const changedProperties = propertyFieldsToChangedPropertiesMap(
          ownerSelection.requiredDefinitions,
          propertyValuesMap,
        )
        if (Object.keys(changedProperties).length) {
          body['custom_properties'] = changedProperties
        }
      }

      setIsCreating(true)
      try {
        const result = await verifiedFetchJSON(repositoriesPath(), {
          method: 'POST',
          body,
        })

        if (result.redirected) {
          navigate(result.url)
        } else {
          const {data} = await result.json()
          if (result.ok) {
            navigate(data.redirect)
          } else {
            setCreateError(data.error)
            setIsCreating(false)
          }
        }
      } catch (e) {
        setCreateError('Something went wrong!')
        setIsCreating(false)
      }
    } else if (!ownerSelection) {
      ownerRepoVisibilitySelectionRef.current?.focusOwnerDropdown()
      return
    } else {
      ownerRepoVisibilitySelectionRef.current?.focusRepoInput()
      return
    }
  }

  const ownerRepoVisibilitySelectionProps: ComponentProps<typeof OwnerRepoVisibilitySelection> = {
    ownerItems,
    ownerSelection,
    repositoryName,
    setRepositoryName,
    onOwnerChange: changeOwner,
    submitClicked,
    repoNameChanged,
    setRepoNameChanged,
    setIsValidRepoName,
    privateModeEnabled,
    helpUrl,
    selectedVisibility,
    setUserSelectedVisibility,
    tradeControlsPrivateRepoCreationWarning,
    tradeControlsUserPrivateRepoCreationWarning,
  }

  const editableDefinitions = useMemo(() => {
    if (!ownerSelection) return []

    return ownerSelection.requiredDefinitions.filter(definition =>
      ownerSelection.editableDefinitionNames.includes(definition.propertyName),
    )
  }, [ownerSelection])

  return (
    <RepoCreationForm
      flash={createError || errorMessage}
      title="Create a new repository"
      description={
        <div>
          A repository contains all project files, including the revision history.
          {repositoryImportLinkDataAttributes && (
            <ImportPrompt repositoryImportLinkDataAttributes={repositoryImportLinkDataAttributes} />
          )}
        </div>
      }
      protectedOrgLogins={protectedOrgLogins}
      onSubmit={createHandler}
      submitButton={
        <Button type="submit" variant="primary" disabled={isCreating} sx={{mt: 3}}>
          {isCreating ? 'Creating repository' : 'Create repository'}
        </Button>
      }
    >
      {hasTemplateRepos && (
        <RepoTemplateMenu
          selectedTemplateRepo={selectedTemplateRepo}
          onRepoTemplateSelect={template => {
            // Reset all initialization options
            setAddReadme(INITIAL_README)
            setSelectedGitignoreTemplate(INITIAL_GITIGNORE)
            setSelectedLicense(INITIAL_LICENSE)
            setSelectedTemplateRepo(template)
            if (template) {
              const templateOwner = ownerItems?.find(x => x.name === template.ownerDisplayLogin)
              templateOwner && changeOwner(templateOwner)
            }
            sendRepoFormClickEvent('TEMPLATE_SELECT', {
              action: 'select',
            })
          }}
          onOpen={() => {
            sendRepoFormClickEvent('TEMPLATE_SELECT', {
              action: 'open',
            })
          }}
          includeAllBranches={includeAllBranches}
          onIncludeAllBranchesChange={setIncludeAllBranches}
        />
      )}
      <Box sx={{mt: 3}}>
        <OwnerRepoVisibilitySelection ref={ownerRepoVisibilitySelectionRef} {...ownerRepoVisibilitySelectionProps}>
          <ReadmeProfileBanner {...{ownerSelection, publicReposAvailable, repositoryName}} />
          {!nameIsRestricted ? (
            <RepoNameGenerator suggestedRepoName={suggestedRepoName} onClick={setRepositoryName} />
          ) : (
            <Box sx={{my: 2}} />
          )}
          {restrictCreateRepositoriesInPersonalNamespace && (
            <Box sx={{my: 3}}>
              <DisabledCreationInPersonalNamespaceMessage />
            </Box>
          )}
          <Description
            description={description}
            onDescriptionChange={setDescription}
            tooLong={descriptionTooLong}
            setTooLong={setDescriptionTooLong}
          />
        </OwnerRepoVisibilitySelection>
        {!selectedTemplateRepo && (
          <Box sx={{mt: 3, borderBottomStyle: 'solid', borderBottomColor: 'border.muted', borderBottomWidth: 1}}>
            <InitializeRepositoryOptions
              addReadme={addReadme}
              onAddReadmeChange={setAddReadme}
              ownerSelection={ownerSelection}
              licensePickerAvailable={!isGitHubEnterprise}
              selectedLicense={selectedLicense}
              onLicenseChange={setSelectedLicense}
              licenses={licenses}
              selectedGitignoreTemplate={selectedGitignoreTemplate}
              onGitignoreTemplateChange={setSelectedGitignoreTemplate}
              currentUserLogin={currentUserLogin}
              helpUrl={helpUrl}
              docsUrls={docsUrls}
            />
          </Box>
        )}
        {ownerSelection && ownerSelection?.installableApps?.length > 0 && (
          <Box sx={{m: 2}}>
            <MarketplaceAppsSelection
              listings={ownerSelection.installableApps}
              selectedListings={selectedMarketplaceApps}
              currentOwnerName={ownerSelection.name}
              currentUserLogin={currentUserLogin}
              handleChange={setSelectedMarketplaceApps}
            />
          </Box>
        )}
        {ownerSelection?.requiredDefinitions && ownerSelection.requiredDefinitions.length > 0 && (
          <Box sx={{borderBottomStyle: 'solid', borderBottomColor: 'border.muted', borderBottomWidth: 1, pb: 3, mt: 3}}>
            <RequiredCustomPropertiesSelection
              definitions={ownerSelection.requiredDefinitions}
              editableDefinitions={editableDefinitions}
              propertyValuesMap={propertyValuesMap}
              setPropertyValue={setPropertyValue}
              revertPropertyValue={revertPropertyValue}
              orgName={ownerSelection.name}
              propertiesOverlayEditorEnabled={propertiesOverlayEditorEnabled}
            />
          </Box>
        )}
      </Box>
      <CreationSummary owner={ownerSelection} visibility={selectedVisibility} action="create" />
    </RepoCreationForm>
  )
}

function ImportPrompt({
  repositoryImportLinkDataAttributes,
}: {
  repositoryImportLinkDataAttributes: {[key: string]: string}
}) {
  const dataAttributes = Object.entries(repositoryImportLinkDataAttributes).reduce(
    (acc, [attribute, value]) => ({...acc, [`data-${attribute}`]: value}),
    {},
  )

  return (
    <span>
      {' '}
      Already have a project repository elsewhere?{' '}
      <Link inline href={repositoryImportsPath()} {...dataAttributes}>
        Import a repository.
      </Link>
    </span>
  )
}

function ReadmeProfileBanner({
  ownerSelection,
  publicReposAvailable,
  repositoryName,
}: {
  ownerSelection?: OwnerSelection
  publicReposAvailable: boolean
  repositoryName: string
}) {
  let content
  if (
    publicReposAvailable &&
    !ownerSelection?.isOrganization &&
    repositoryName.toLowerCase() === ownerSelection?.name.toLowerCase()
  ) {
    content = (
      <div>
        <strong>
          {ownerSelection?.name}/{repositoryName}
        </strong>{' '}
        is a <em>special</em>  repository that you can use to add a <code>README.md</code> to your GitHub profile.
        Make sure it&rsquo;s public and initialize it with a <strong>README</strong> to get started.
      </div>
    )
  } else if (
    ownerSelection?.isOrganization &&
    !ownerSelection?.enterpriseManagedUserEnabled &&
    publicReposAvailable &&
    repositoryName.toLowerCase() === '.github'
  ) {
    content = (
      <div>
        <strong>{ownerSelection?.name}/.github</strong> is a <em>special</em>  repository that you can use to add{' '}
        <code>README.md</code> to your public organization profile, visible to anyone. Make sure it&rsquo;s public and
        and initialize it with a <strong>README</strong> in the <strong>profile</strong> directory to get started.
      </div>
    )
  } else if (ownerSelection?.isOrganization && repositoryName.toLowerCase() === '.github-private') {
    content = (
      <div>
        <strong>{ownerSelection?.name}/.github-private</strong> is a <em>special</em>  repository that you can use
        to add a <code>README.md</code> to your organization member profile, visible only to organization members.
        It&rsquo;s private and initialize it with a <strong>README</strong> in the <strong>profile</strong> directory to
        get started.
      </div>
    )
  }

  return content ? (
    <Flash sx={{display: 'flex', gap: 2, my: 2}} data-testid="special-repo-name-banner">
      <Box as="img" alt="Whispering" src="/images/mona-whisper.gif" sx={{width: 48, height: 48, mt: -2}} />
      {content}
    </Flash>
  ) : null
}

try{ Create.displayName ||= 'Create' } catch {}
try{ CreateForm.displayName ||= 'CreateForm' } catch {}
try{ ImportPrompt.displayName ||= 'ImportPrompt' } catch {}
try{ ReadmeProfileBanner.displayName ||= 'ReadmeProfileBanner' } catch {}