import type {Visibility} from '@github-ui/repos-list/repos-list-types'
import {SafeHTMLBox, type SafeHTMLString} from '@github-ui/safe-html'
import {InfoIcon, LockIcon, OrganizationIcon, RepoIcon} from '@primer/octicons-react'
import {Box, Flash, FormControl, Link, Octicon, Radio, RadioGroup} from '@primer/react'
import {useEffect} from 'react'

import type {OwnerSelection} from '../types/repo-creation-types'

export function VisibilitySelection({
  ownerSelection,
  privateModeEnabled,
  selectedVisibility,
  helpUrl,
  tradeControlsPrivateRepoCreationWarning,
  tradeControlsUserPrivateRepoCreationWarning,
  onChange,
}: {
  ownerSelection?: OwnerSelection
  privateModeEnabled: boolean
  selectedVisibility: Visibility
  helpUrl: string
  tradeControlsPrivateRepoCreationWarning: SafeHTMLString
  tradeControlsUserPrivateRepoCreationWarning: SafeHTMLString
  onChange: (visibility: Visibility) => void
}) {
  let showPublic = false
  let showInternal = false
  let showPrivate = false

  const orgPolicyRestrictedVisibilities: string[] = []
  const otherWarnings: JSX.Element[] = []

  if (ownerSelection) {
    const {
      publicRestrictedByPolicy,
      internalRestrictedByPolicy,
      privateRestrictedByPolicy,
      privateRestrictedByPlan,
      displayedVisibilities,
      hasTradeRestrictions,
      adminableByCurrentUser,
      isOrganization,
    } = ownerSelection

    // Determine public repo option availability
    if (publicRestrictedByPolicy) {
      orgPolicyRestrictedVisibilities.push('public')
    } else if (displayedVisibilities.includes('public')) {
      showPublic = true
    }

    // Determine internal repo option availability
    if (internalRestrictedByPolicy) {
      orgPolicyRestrictedVisibilities.push('internal')
    } else if (displayedVisibilities.includes('internal')) {
      showInternal = true
    }

    // Determine private repo option availability

    showPrivate = true

    const individualTradeControls = !isOrganization && hasTradeRestrictions
    if (individualTradeControls) {
      otherWarnings.push(<SafeHTMLBox html={tradeControlsUserPrivateRepoCreationWarning} />)
      showPrivate = false
    }

    const orgTradeControls = isOrganization && hasTradeRestrictions

    const tradeRestrictedOrgAdmin = orgTradeControls && adminableByCurrentUser
    if (tradeRestrictedOrgAdmin) {
      otherWarnings.push(<SafeHTMLBox html={tradeControlsPrivateRepoCreationWarning} />)
      showPrivate = false
    }

    const tradeRestrictedNonOrgAdmin = orgTradeControls && !adminableByCurrentUser
    if (privateRestrictedByPolicy || tradeRestrictedNonOrgAdmin) {
      orgPolicyRestrictedVisibilities.push('private')
      showPrivate = false
    }

    if (privateRestrictedByPlan) {
      otherWarnings.push(<div>Your current plan does not support private repositories.</div>)
      showPrivate = false
    }
  }

  useEffect(() => {
    const selectableVisibilities: Visibility[] = []

    if (showPublic) selectableVisibilities.push('public')
    if (showInternal) selectableVisibilities.push('internal')
    if (showPrivate) selectableVisibilities.push('private')

    if (!selectableVisibilities.includes(selectedVisibility)) {
      if (ownerSelection && selectableVisibilities.includes(ownerSelection.defaultVisibility)) {
        onChange(ownerSelection.defaultVisibility)
      } else if (selectableVisibilities[0]) {
        onChange(selectableVisibilities[0])
      }
    }
  }, [onChange, ownerSelection, selectedVisibility, showPublic, showPrivate, showInternal])

  const selectionWarningProps = {
    orgPolicyRestrictedVisibilities,
    otherWarnings,
    ownerSelection,
  }

  return (
    <div>
      {(!ownerSelection || orgPolicyRestrictedVisibilities.length > 0 || otherWarnings.length > 0) && (
        <SelectionWarningBanner {...selectionWarningProps} />
      )}
      <RadioGroup name="visibilityGroup" onChange={value => onChange(value as Visibility)}>
        <RadioGroup.Label visuallyHidden>Visibility</RadioGroup.Label>
        {showPublic && (
          <PublicSelection privateModeEnabled={privateModeEnabled} checked={selectedVisibility === 'public'} />
        )}
        {showInternal && (
          <InternalSelection
            checked={selectedVisibility === 'internal'}
            helpUrl={helpUrl}
            ownerSelection={ownerSelection}
          />
        )}
        {showPrivate && <PrivateSelection checked={selectedVisibility === 'private'} />}
      </RadioGroup>
    </div>
  )
}

const SelectionWarningBanner = ({
  orgPolicyRestrictedVisibilities,
  otherWarnings,
  ownerSelection,
}: {
  orgPolicyRestrictedVisibilities: string[]
  otherWarnings: JSX.Element[]
  ownerSelection?: OwnerSelection
}) => {
  const commaConcat = (words: string[]) => {
    if (words.length === 1) {
      return words[0]
    } else if (words.length === 2) {
      return `${words[0]} or ${words[1]}`
    } else {
      return `${words.slice(0, -1).join(', ')}, or ${words.slice(-1)}`
    }
  }

  const bannerContent = () => {
    const orgPolicyWarning = `You may not create ${commaConcat(
      orgPolicyRestrictedVisibilities,
    )} repositories by organization policy.`
    const otherWarningsList = (
      <Box as="ul" sx={{ml: 3}}>
        {otherWarnings.map((warning, idx) => (
          <li key={idx}>{warning}</li>
        ))}
      </Box>
    )
    if (!ownerSelection) {
      return 'Please choose an owner to see the available visibility options.'
    } else if (orgPolicyRestrictedVisibilities.length > 0 && otherWarnings.length === 0) {
      return orgPolicyWarning
    } else if (otherWarnings.length > 0 && orgPolicyRestrictedVisibilities.length === 0) {
      return (
        <div>
          Some visibility options are unavailable:
          {otherWarningsList}
        </div>
      )
    } else {
      return (
        <div>
          {orgPolicyWarning} Some additional visibility options are also unavailable:
          {otherWarningsList}
        </div>
      )
    }
  }

  return (
    <Flash sx={{my: 2}}>
      <Box sx={{display: 'flex', alignItems: 'baseline'}}>
        <div>
          <Octicon icon={InfoIcon} />
        </div>
        <Box sx={{display: 'inline-flex', flexDirection: 'column', ml: 1}}>{bannerContent()}</Box>
      </Box>
    </Flash>
  )
}

function PublicSelection({
  privateModeEnabled,
  checked,
}: {
  privateModeEnabled: boolean
  checked: boolean
  ownerSelection?: OwnerSelection
}) {
  const publicRepoCaption = () => {
    const publicAudience = privateModeEnabled ? 'Any logged in user' : 'Anyone on the internet'
    return `${publicAudience} can see this repository. You choose who can commit.`
  }

  return (
    <FormControl>
      <Radio value="public" checked={checked} />
      <FormControl.LeadingVisual>
        <Octicon icon={RepoIcon} size={24} sx={{color: 'fg.muted'}} />
      </FormControl.LeadingVisual>
      <FormControl.Label>Public</FormControl.Label>
      <FormControl.Caption>{publicRepoCaption()}</FormControl.Caption>
    </FormControl>
  )
}

function InternalSelection({
  checked,
  helpUrl,
  ownerSelection,
}: {
  checked: boolean
  helpUrl: string
  ownerSelection?: OwnerSelection
}) {
  const internalRepoCaption = () => (
    <div>
      {ownerSelection && ownerSelection.businessName}{' '}
      <Link href={`${helpUrl}/articles/roles-for-an-enterprise-account#enterprise-members`} inline>
        enterprise members
      </Link>{' '}
      can see this repository. You can choose who can commit.
    </div>
  )

  return (
    <FormControl>
      <Radio value="internal" checked={checked} />
      <FormControl.LeadingVisual>
        <Octicon icon={OrganizationIcon} size={24} sx={{color: 'fg.muted'}} />
      </FormControl.LeadingVisual>
      <FormControl.Label>Internal</FormControl.Label>
      <FormControl.Caption>{internalRepoCaption()}</FormControl.Caption>
    </FormControl>
  )
}

function PrivateSelection({checked}: {checked: boolean}) {
  const privateRepoCaption = () => 'You choose who can see and commit to this repository.'

  return (
    <FormControl>
      <Radio value="private" checked={checked} />
      <FormControl.LeadingVisual>
        <Octicon icon={LockIcon} size={24} sx={{color: 'fg.muted'}} />
      </FormControl.LeadingVisual>
      <FormControl.Label>Private</FormControl.Label>
      <FormControl.Caption>{privateRepoCaption()}</FormControl.Caption>
    </FormControl>
  )
}

try{ VisibilitySelection.displayName ||= 'VisibilitySelection' } catch {}
try{ SelectionWarningBanner.displayName ||= 'SelectionWarningBanner' } catch {}
try{ PublicSelection.displayName ||= 'PublicSelection' } catch {}
try{ InternalSelection.displayName ||= 'InternalSelection' } catch {}
try{ PrivateSelection.displayName ||= 'PrivateSelection' } catch {}