import {CustomPropertyValuesTable} from '@github-ui/custom-properties-editing'
import type {PropertyField} from '@github-ui/custom-properties-editing/use-edit-custom-properties'
import type {PropertyDefinition, PropertyValue} from '@github-ui/custom-properties-types'
import {Text} from '@primer/react'

export function RequiredCustomPropertiesSelection({
  definitions,
  editableDefinitions,
  propertyValuesMap,
  setPropertyValue,
  revertPropertyValue,
  orgName,
  propertiesOverlayEditorEnabled,
}: {
  definitions: PropertyDefinition[]
  editableDefinitions: PropertyDefinition[]
  propertyValuesMap: Record<string, PropertyField>
  setPropertyValue: (propertyName: string, value: PropertyValue) => void
  revertPropertyValue: (propertyName: string) => void
  orgName: string
  // TODO: cleanup with custom_properties_edit_modal FF.
  propertiesOverlayEditorEnabled?: boolean
}) {
  return (
    <div data-testid="custom-properties-selection">
      <Text sx={{display: 'block', fontSize: 3}}>Required properties</Text>
      <Text sx={{display: 'block', color: 'fg.muted', mb: 2}}>
        This repository will be initialized with the following, per organization policy.
      </Text>
      <CustomPropertyValuesTable
        definitions={definitions}
        editableDefinitions={editableDefinitions}
        propertyValuesMap={propertyValuesMap}
        setPropertyValue={setPropertyValue}
        revertPropertyValue={revertPropertyValue}
        showLockMessages={false}
        showUndo={false}
        orgName={orgName}
        propertiesOverlayEditorEnabled={propertiesOverlayEditorEnabled}
      />
    </div>
  )
}

try{ RequiredCustomPropertiesSelection.displayName ||= 'RequiredCustomPropertiesSelection' } catch {}