import {OwnerDropdown, type OwnerItem} from '@github-ui/owner-dropdown'
import {Box, Flash, Text} from '@primer/react'
import {forwardRef, useImperativeHandle, useMemo, useRef} from 'react'

import {useRepoOwnerItemsPath} from '../hooks/use-repo-owner-items-path'
import {RepoNameInput} from './RepoNameInput'

export const OwnerRepoSelection = forwardRef(
  (
    {
      ownerItems,
      selectedOwner,
      repositoryName,
      onRepoChange,
      onOwnerChange,
      hideRepoBlankCheck = false,
      hideSelectOwnerCheck,
      readonlyRepo = false,
      onValidityChange,
    }: OwnerRepoSelectionProps,
    ref,
  ) => {
    const ownerDropdownRef = useRef<HTMLButtonElement>(null)
    const repoInputRef = useRef<HTMLInputElement>(null)
    const repoOwnerItemsPath = useRepoOwnerItemsPath()

    useImperativeHandle(ref, () => ({
      focusOwnerDropdown: () => {
        ownerDropdownRef.current?.focus()
      },
      focusRepoInput: () => {
        repoInputRef.current?.focus()
      },
    }))

    const hasPermission = !selectedOwner?.disabled
    const ownerDropdown = useMemo(
      () => (
        <OwnerDropdown
          ref={ownerDropdownRef}
          initialOwnerItems={ownerItems}
          ownerItemsPath={repoOwnerItemsPath}
          selectedOwner={selectedOwner}
          onOwnerChange={onOwnerChange}
          hideSelectOwnerCheck={hideSelectOwnerCheck}
        />
      ),
      [ownerItems, repoOwnerItemsPath, selectedOwner, hideSelectOwnerCheck, onOwnerChange],
    )
    return (
      <div data-hpc>
        <fieldset>
          <legend className="sr-only">Repository owner and name</legend>
          <Box sx={{display: 'flex', flexDirection: ['column', 'row']}}>
            {/* The 21px top margin is hardocded so that the "/" is correctly aligned regardless of the validation state
          of the OwnerDropdown and NewRepoInput components (as opposed to using flexbox) */}
            {ownerDropdown}
            {hasPermission && (
              <>
                <Text sx={{fontSize: 4, mx: 2, mt: '21px', display: ['none', 'block']}}>/</Text>
                <Box sx={{mt: [2, 0]}}>
                  <RepoNameInput
                    ref={repoInputRef}
                    repositoryName={repositoryName}
                    readonly={readonlyRepo}
                    hideBlankCheck={hideRepoBlankCheck}
                    selectedOwnerName={selectedOwner?.name}
                    onChange={onRepoChange}
                    onValidityChange={onValidityChange}
                  />
                </Box>
              </>
            )}
          </Box>
        </fieldset>
        {!hasPermission && (
          <Flash sx={{mt: 2}} variant="warning">
            You dont appear to have permission to create repositories for this organization. Sorry about that.
          </Flash>
        )}
      </div>
    )
  },
)

OwnerRepoSelection.displayName = 'OwnerRepoSelection'

interface OwnerRepoSelectionProps {
  ownerItems?: OwnerItem[]
  selectedOwner?: OwnerItem
  repositoryName: string
  onRepoChange: (repositoryName: string) => void
  onOwnerChange: (selectedOwner: OwnerItem) => void
  hideRepoBlankCheck?: boolean
  hideSelectOwnerCheck?: boolean
  readonlyRepo?: boolean
  onValidityChange: (valid: boolean) => void
}

export interface OwnerRepoSelectionRef {
  focusOwnerDropdown: () => void
  focusRepoInput: () => void
}
