import {debounce} from '@github/mini-throttle'
import {useCurrentRepository} from '@github-ui/current-repository'
import type {PropertyValuesRecord} from '@github-ui/custom-properties-types'
import {repoTransferPath, repoTransferTeamSuggestionsPath} from '@github-ui/paths'
import {useNavigate} from '@github-ui/use-navigate'
import {verifiedFetch, verifiedFetchJSON} from '@github-ui/verified-fetch'
import {SearchIcon} from '@primer/octicons-react'
import {Box, Button, Checkbox, Flash, FormControl, Pagination, Text, TextInput} from '@primer/react'
import {useCallback, useRef, useState} from 'react'

import type {Team} from '../types/repo-creation-types'
import {TeamListItem} from './TeamListItem'

interface RepoTransferWithTeamSelectionBody {
  new_owner: string
  new_name: string
  custom_properties?: PropertyValuesRecord
  commit: 'Transfer'
  teams_selection: '1'
  team_ids?: string[]
}

export function TeamSelection({
  teams,
  initialPageCount,
  onTeamFetch,
  newOwner,
  repositoryName,
  errorHandler,
  selectedProperties,
}: {
  teams: Team[]
  initialPageCount: number
  onTeamFetch: (teams: Team[]) => void
  newOwner: string
  repositoryName: string
  errorHandler: () => void
  selectedProperties?: PropertyValuesRecord
}) {
  const [selectedTeamIds, setSelectedTeamIds] = useState<string[]>([])
  const [currentPage, setCurrentPage] = useState(1)
  const [query, setQuery] = useState('')
  const [pageCount, setPageCount] = useState(initialPageCount)
  const [noTeamsOptionSelected, setNoTeamsOptionSelected] = useState(false)
  const [showFlash, setShowFlash] = useState(false)
  const [isTransferring, setIsTransferring] = useState(false)

  const checkboxRef = useRef<HTMLInputElement>(null)
  const navigate = useNavigate()

  const handleChange = (id: string): void => {
    setNoTeamsOptionSelected(false)
    if (selectedTeamIds.includes(id)) {
      setSelectedTeamIds(selectedTeamIds.filter(teamId => teamId !== id))
    } else {
      setSelectedTeamIds([...selectedTeamIds, id])
    }
  }

  const repo = useCurrentRepository()

  const transferHandler = async () => {
    setShowFlash(false)
    if (noTeamsOptionSelected || selectedTeamIds.length > 0) {
      const body: RepoTransferWithTeamSelectionBody = {
        ['new_owner']: newOwner,
        commit: 'Transfer',
        ['teams_selection']: '1',
        ['new_name']: repositoryName,
      }

      const teamIds: string[] = []
      for (const id of selectedTeamIds) {
        teamIds.push(id)
      }

      if (teamIds.length) {
        body['team_ids'] = teamIds
      }

      if (Object.keys(selectedProperties || {})?.length) {
        body['custom_properties'] = selectedProperties
      }

      try {
        setIsTransferring(true)
        const result = await verifiedFetchJSON(repoTransferPath(repo), {
          method: 'POST',
          body,
        })
        if (result.ok && result.redirected) {
          window.location.assign(result.url)
        } else if (result.ok) {
          const json = await result.json()
          navigate(json.data.redirect)
        } else {
          setIsTransferring(false)
          errorHandler()
        }
      } catch (e) {
        setIsTransferring(false)
        errorHandler()
      }
    } else {
      setIsTransferring(false)
      checkboxRef.current?.focus()
      setShowFlash(true)
    }
  }

  const fetchTeamsFromServer = async (queryString: string, page = 1) => {
    try {
      const result = await verifiedFetch(
        `${repoTransferTeamSuggestionsPath(repo)}?page=${page}&new_owner=${newOwner}&query=${queryString}`,
      )

      if (result.ok) {
        const {data} = await result.json()
        setCurrentPage(page)
        setPageCount(data.pageCount)
        onTeamFetch(data.teams)
      } else {
        errorHandler()
      }
    } catch (e) {
      errorHandler()
    }
  }

  const onPageChange = async (evt: React.MouseEvent, page: number) => {
    evt.preventDefault()
    await fetchTeamsFromServer(query, page)
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const debounceFetchTeamsFromServer = useCallback(
    debounce(async (value: string) => {
      setQuery(value)
      await fetchTeamsFromServer(value)
    }, 200),
    [setQuery, fetchTeamsFromServer],
  )

  return (
    <Box sx={{my: 6}} className="container-sm">
      <h2>Team access</h2>
      {showFlash && (
        <Flash variant="danger" sx={{my: 2}}>
          Please either select no teams or choose at least one team to have access to{' '}
          <strong>{`${newOwner}/${repositoryName}`}</strong>.
        </Flash>
      )}
      <Text as="p" sx={{color: 'fg.muted'}} data-testid="query-text-input-label">
        Please select any teams you wish to have access to <strong>{`${newOwner}/${repositoryName}`}</strong>.
      </Text>
      <FormControl sx={{mt: 2, mb: 3}}>
        <Checkbox
          onChange={() => {
            setNoTeamsOptionSelected(!noTeamsOptionSelected)
            setSelectedTeamIds([])
          }}
          checked={noTeamsOptionSelected}
          ref={checkboxRef}
          aria-label={`Please select any teams you wish to have access to ${newOwner}/${repositoryName}`}
        />
        <FormControl.Label sx={{fontWeight: 'normal'}}>
          Do not give any teams access to <strong>{`${newOwner}/${repositoryName}`}</strong>.
        </FormControl.Label>
      </FormControl>
      <TextInput
        block
        sx={{mb: 2}}
        leadingVisual={SearchIcon}
        placeholder="Find a team"
        aria-label="Find a team"
        onChange={e => debounceFetchTeamsFromServer(e.target.value)}
      />
      <Box sx={{mb: 2}} data-testid="team-list">
        {teams.map(team => (
          <TeamListItem key={team.id} team={team} selectedTeamIds={selectedTeamIds} onChange={handleChange} />
        ))}
      </Box>
      <Pagination pageCount={pageCount} currentPage={currentPage} onPageChange={onPageChange} />
      <Button
        className="float-right"
        variant="primary"
        onClick={transferHandler}
        data-sudo-required={true}
        disabled={isTransferring}
      >
        {isTransferring ? 'Transferring repository' : 'Transfer'}
      </Button>
    </Box>
  )
}

try{ TeamSelection.displayName ||= 'TeamSelection' } catch {}