import {Box, Button} from '@primer/react'

export function RepoNameGenerator({
  suggestedRepoName,
  onClick,
}: {
  suggestedRepoName: string
  onClick: (suggestedName: string) => void
}) {
  return (
    <Box sx={{my: 2}}>
      Great repository names are short and memorable. Need inspiration? How about&nbsp;
      <Button
        variant="invisible"
        sx={{
          fontWeight: 'bold',
          color: 'success.fg',
          display: 'inline',
          px: 0,
          ':hover:not([disabled])': {bg: 'canvas.default'},
        }}
        aria-label={`Use suggested repository name ${suggestedRepoName}`}
        onClick={() => onClick(suggestedRepoName)}
      >
        {suggestedRepoName}
      </Button>
      &nbsp;?
    </Box>
  )
}

try{ RepoNameGenerator.displayName ||= 'RepoNameGenerator' } catch {}