import type {OwnerItem} from '@github-ui/owner-dropdown'
import type {Visibility} from '@github-ui/repos-list/repos-list-types'

import {InfoMessage} from './InfoMessage'

interface Props {
  owner?: OwnerItem
  topBorder?: boolean
}

interface CreateProps extends Props {
  visibility: Visibility
  action: 'create'
}

interface ForkProps extends Props {
  action: 'fork'
}

function isCreateProps(props: CreateProps | ForkProps): props is CreateProps {
  return props.action === 'create'
}

export function CreationSummary(props: CreateProps | ForkProps) {
  const {owner, topBorder = false} = props
  if (!owner) {
    return null
  }

  const {name, businessName, isOrganization} = owner
  const targetOrgName = isOrganization ? name : null
  const orgText = targetOrgName
    ? ` in the ${targetOrgName} organization${businessName ? ` (${businessName})` : ''}`
    : ' in your personal account'

  let message = ''
  if (isCreateProps(props)) {
    const {visibility} = props
    message = `You are creating a${visibility === 'internal' ? 'n' : ''} ${visibility} repository${orgText}.`
  } else {
    message = `You are creating a fork${orgText}.`
  }

  return <InfoMessage message={message} topBorder={topBorder} />
}

try{ CreationSummary.displayName ||= 'CreationSummary' } catch {}