import {InfoIcon} from '@primer/octicons-react'
import {Box, Octicon} from '@primer/react'

export function InfoMessage({message, topBorder = true}: {message: string; topBorder?: boolean}) {
  return (
    <Box
      sx={{
        fontSize: 14,
        color: 'fg.muted',
        py: 3,
        borderBottomColor: 'border.muted',
        borderBottomWidth: 1,
        borderBottomStyle: 'solid',
        ...(topBorder && {
          borderTopColor: 'border.muted',
          borderTopWidth: 1,
          borderTopStyle: 'solid',
        }),
      }}
    >
      <Octicon icon={InfoIcon} sx={{mx: 1}} />
      <span>{message}</span>
    </Box>
  )
}

export function DisabledCreationInPersonalNamespaceMessage() {
  return (
    <InfoMessage message="Creating repositories in your personal namespace is disabled in this GitHub Enterprise instance." />
  )
}

try{ InfoMessage.displayName ||= 'InfoMessage' } catch {}
try{ DisabledCreationInPersonalNamespaceMessage.displayName ||= 'DisabledCreationInPersonalNamespaceMessage' } catch {}